import React, {useState, useCallback, useEffect, useRef} from 'react'
import {Avatar, GiftedChat, LoadEarlier, MessageText} from 'react-native-gifted-chat'
import {useApi} from "../hooks/api";
import 'dayjs/locale/fa'
import {ImageBackground, KeyboardAvoidingView, Text, TextInput} from "react-native";
import {StyleSheet, ActivityIndicator, View} from "react-native";
import {
    Bubble,
    SystemMessage,
    IMessage,
    InputToolbar,
    Send,
    SendProps,
    User,
    Day
} from 'react-native-gifted-chat'
import {useAuth} from "../hooks/auth";
import dayjs from 'dayjs'
import jalaliday from 'jalaliday'
import {e2p} from "../lib/helper";
import LoadingScreen from "../Screens/LoadingScreen";
import Clipboard from "@react-native-clipboard/clipboard";
import {SheetManager} from "react-native-actions-sheet";

dayjs.extend(jalaliday)
dayjs.calendar('jalali') // Jalali Calendar

const bg = require('../assets/images/bg-2-min.jpg')

export function Chat(props) {

    Bubble.defaultProps = {
        touchableProps: {},
        onLongPress: null,
        renderMessageImage: null,
        renderMessageText: null,
        renderCustomView: null,
        renderTime: null,
        currentMessage: {
            text: null,
            createdAt: null,
            image: null,
        },
        nextMessage: {},
        previousMessage: {},
        containerStyle: {},
        wrapperStyle: {},
        tickStyle: {},
        containerToNextStyle: {},
        containerToPreviousStyle: {},
    }

    const {getUser} = useAuth()

    const [messages, setMessages] = useState(null);
    const [customText, setCustomText] = useState('');
    const [errors, setErrors] = useState(null);
    const [status, setStatus] = useState(null);
    const [current, setCurrent] = useState(1);
    const [last, setLast] = useState(null);
    const [userLoading, setUserLoading] = useState(null);
    const [user, setUser] = useState(null);
    const ref = useRef();

    const {loading, sendMessage, getMessages} = useApi()


    useEffect(() => {
        setUserLoading(true)

        async function fetchData() {
            const user = await getUser()
            setUser(user)
        }

        fetchData().then(() => {
            setUserLoading(false)
        })
    }, [])

    useEffect(() => {
        getMessages({setStatus, setErrors, setCurrent, setLast, setMessages, page: current, type: props.type})
    }, [current])

    const renderAvatar = (props) => (
        <Avatar
            {...props}
        />
    );
    const renderBubble = (props) => {
        return <Bubble {...props}/>
    }
    const renderLoadEarlier = (props) => {
        return <LoadEarlier {...props}
                            label={'  '}
        />
    }

    const renderSend = (props) => {
        return <Send textStyle={{fontFamily: 'DanaRegular'}} {...props}
                     label={''}
        />
    }

    const renderUsername = ({name}) => {
        if (name) {
            return (
                <Text
                    style={{
                        fontFamily: 'DanaRegular',
                        fontSize: 10,
                    }}
                >{name}</Text>
            )
        }
        return null
    }

    const renderMessageText = (props) => {
        return <MessageText {...props} customTextStyle={{
            fontFamily: 'DanaRegular'
        }}></MessageText>
    }
    const onLongPress = (context, message) => {

        const options = [
            '',
            '  ',
            '',
        ];

        const cancelButtonIndex = options.length - 1;

        context.actionSheet().showActionSheetWithOptions({
                options,
                cancelButtonIndex,
            },
            async (buttonIndex) => {
                switch (buttonIndex) {

                    // case 0:
                    //     // set message text to input
                    //     // bring up the keyboard
                    //     // change text of send to edit
                    //     // call edit api
                    //     // change state
                    //     // clear the input
                    //     // bring down the keyboard
                    //     if (ref.current) {
                    //         ref.current.setNativeProps({text: message.text})
                    //         ref.current.focus()
                    //     }
                    //     break;

                    case 0:
                        await SheetManager.show('delete-message-sheet', {
                            payload: {
                                message: message
                            },
                        }).then((res) => {
                            if (res.status === 'ok') {
                                setMessages(previousMessages => previousMessages.filter(function (el) {
                                    return el.id !== message.id;
                                }))
                            }
                        })
                        break;
                }
            });

    }

    const rendert = () => (
        <Text>
            
        </Text>
    )

    const onSend = useCallback((messages = []) => {
        setMessages(previousMessages => GiftedChat.append(previousMessages, messages))
        sendMessage({setStatus, setErrors, text: messages[0].text, type: props.type})
    }, [])

    function renderDay(props) {
        dayjs.extend(jalaliday)
        dayjs.locale('fa')
        const date = dayjs(props.currentMessage.createdAt);

        return <Text style={{
            textAlign: 'center',
            color: '#989898',
            fontFamily: 'DanaRegular'
        }}>{e2p(date.format('DD MMMM YYYY'))}</Text>
    }

    // const renderInputToolbar = (props) => {
    //     return <InputToolbar {...props}></InputToolbar>
    // }

    const styles = StyleSheet.create({
        backgroundImage: {
            flex: 1,
            width: null,
            height: null,
            resizeMode: 'cover'
        },
    });

    return (
        <ImageBackground source={bg} resizeMode="cover" style={styles.backgroundImage}>
            <GiftedChat
                renderLoading={() => <ActivityIndicator size="large" color="#0000ff"/>}
                placeholder={'    ...'}
                loadEarlier={current < last ? true : false}
                textInputRef={ref}
                onInputTextChanged={text => setCustomText(text)}
                onLoadEarlier={() => {
                    if (last === null) {
                        setCurrent(prevState => prevState + 1)
                    } else if (current < last) {
                        setCurrent(prevState => prevState + 1)
                    }
                }}
                messagesContainerStyle={{}}
                renderLoadEarlier={renderLoadEarlier}
                // isLoadingEarlier={true}
                renderBubble={renderBubble}
                renderAvatar={renderAvatar}
                multiline={true}
                scrollToBottom={true}
                // alignTop={true}
                infiniteScroll={true}
                messages={messages}
                renderUsernameOnMessage={false}
                renderUsername={renderUsername}
                // renderAvatarOnTop={true}
                // showUserAvatar={true}
                // showAvatarForEveryMessage={true}
                alwaysShowSend={true}
                // locale={'fa'}
                // dateFormat={'dddd D MMMM YYYY'}
                // dateFormat={'dddd'}
                renderSend={renderSend}
                // renderDay={renderDay}
                // inverted={true}
                // renderInputToolbar={renderInputToolbar}
                renderMessageText={renderMessageText}
                onLongPress={onLongPress}
                // isTyping={true}
                onSend={messages => onSend(messages)}
                user={{
                    _id: user?.id,
                }}
            />
        </ImageBackground>

    )
}
