import {Button, StyleSheet, Text, TextInput, TouchableOpacity, View} from "react-native";
import React, {useEffect, useState} from "react";
import Fab from "../../components/Fab";
import {useApi} from "../../hooks/api";
import MaterialIcons from "@expo/vector-icons/MaterialIcons";

export default function EditNoteScreen({navigation, route}) {

    const {storeNote} = useApi()

    const styles = StyleSheet.create({})

    const [errors, setErrors] = useState(null);
    const [status, setStatus] = useState(null);
    const [text, setText] = useState(null)
    const [id, setId] = useState(null)

    const saveText = async () => {
        if (text !== '') {
            await storeNote({setStatus, setErrors, text, id}).then(() => {
                navigation.goBack()
            })
        }
    }

    useEffect(() => {
        if (route?.params.type !== 'new'){
            setId(route.params?.note.id)
            setText(route.params?.note.text)
        }

    }, [route.params?.note, navigation]);

    useEffect(() => {
        navigation.setOptions({
            title: route?.params.type === 'new' ? ' ' : ' ',
            headerRight: () => (
                <MaterialIcons
                    onPress={saveText}
                    name="save" size={24}
                    color="black"/>)
        });
    }, [text, id, navigation]);

    return (
        <View style={{}}>
            <TextInput
                style={{
                    textAlignVertical: "top", // android fix for centering it at the top corner
                    textAlign: 'right',
                    padding: 10,
                    height: '100%',
                }}
                multiline={true}
                onChangeText={(text) => setText(text)}
                value={text}/>
        </View>
    );
}
