import {
    Alert,
    Button,
    Dimensions,
    Modal,
    Pressable,
    ScrollView,
    StyleSheet,
    Text, TextInput,
    TouchableOpacity,
    View
} from "react-native";
import React, {useEffect, useState} from "react";
import {SheetManager, SheetProvider} from "react-native-actions-sheet";
import {BarChart, LineChart} from "react-native-chart-kit";
import {convertRtl} from 'react-native-rtl-reshaper';
import Video from "../components/Video";
import {NavigationContainer} from '@react-navigation/native';
import {createBottomTabNavigator} from '@react-navigation/bottom-tabs';
import {Ionicons} from "@expo/vector-icons";
import MyModal from "../components/MyModal";
import {useApi} from "../hooks/api";
import LoadingScreen from "./LoadingScreen";
import Fab from "../components/Fab";
import ExerciseCharts from "./NotebookScreens/ExerciseCharts";
import ArmCharts from "./NotebookScreens/ArmCharts";
import NotesScreen from "./NotebookScreens/NotesScreen";
import FeedbackScreen from "./NotebookScreens/FeedbackScreen";
import MoodCharts from "./NotebookScreens/MoodCharts";

export default function NotebookScreen({navigation}) {

    const Tab = createBottomTabNavigator();
    function Reports() {
        return (
            <ScrollView>
                <View style={{
                    direction: 'rtl',
                    backgroundColor: '#f2f2f2',
                    width: '100%',
                    height: '100%',
                    alignItems: 'center',
                    marginBottom: 10
                }}>
                    <View style={{
                        marginTop: 15,
                        width: '95%',
                        backgroundColor: '#ffffff',
                        shadowColor: '#000000',
                        borderRadius: 5,
                        shadowOffset: {
                            width: 0,
                            height: 2,
                        },
                        shadowOpacity: 0.25,
                        shadowRadius: 3.84,
                        elevation: 3,
                    }}>
                        <Text style={{
                            paddingRight: 10,
                            paddingTop: 10,
                            fontSize: 16,
                            fontFamily: 'DanaBold',
                        }}> </Text>
                        <Text style={{
                            paddingRight: 10,
                            fontSize: 14,
                            color: '#2d2d2d',
                            fontFamily: 'DanaLight',
                        }}>              .</Text>
                    </View>
                    <ExerciseCharts></ExerciseCharts>
                    <ArmCharts></ArmCharts>
                    <MoodCharts></MoodCharts>
                </View>
            </ScrollView>
        );
    }
    function NoteBook() {
        return (
            <NotesScreen stackNavigation={navigation}></NotesScreen>
        );
    }
    function Feedback() {
        return (
            <FeedbackScreen></FeedbackScreen>
        )
    }

    return (
        <Tab.Navigator screenOptions={({route}) => ({
            tabBarIcon: ({focused, color, size}) => {
                let iconName;

                if (route.name === 'Reports') {
                    iconName = focused
                        ? 'bar-chart'
                        : 'bar-chart-outline';
                } else if (route.name === 'NoteBook') {
                    iconName = focused ? 'book' : 'book-outline';
                } else if (route.name === 'Feedback') {
                    iconName = focused ? 'bulb' : 'bulb-outline';
                }

                // You can return any component that you like here!
                return <Ionicons name={iconName} size={size} color={color}/>;
            },
            tabBarActiveTintColor: 'tomato',
            tabBarInactiveTintColor: 'gray',
            headerShown: false,
        })}>
            <Tab.Screen name="Reports" component={Reports}
                        options={{
                            tabBarLabelStyle: {
                                fontFamily: 'DanaRegular'
                            },
                            title: ''
                        }}
            />
            <Tab.Screen name="NoteBook" component={NoteBook}
                        options={{
                            tabBarLabelStyle: {
                                fontFamily: 'DanaRegular'
                            },
                            title: '',
                        }}
            />
            <Tab.Screen name="Feedback" component={Feedback}
                        options={{
                            tabBarLabelStyle: {
                                fontFamily: 'DanaRegular'
                            },
                            title: ''
                        }}
            />
        </Tab.Navigator>
    );
}

