    const getMessages = async ({setStatus, setErrors, setMessages, setCurrent, setLast, ...props}) => {
        await csrf()
        setLoading(true)
        setStatus(null)
        setErrors(null)

        axios
            .get('/api/messages', {
                headers: await getHeaders(),
                params: props
            })
            .then((res) => {
                let messages = res.data.data.data
                setMessages((prev) => {
                    if (prev !== null) {
                        const old = prev.filter(om => !messages.some(newm => newm._id === om._id));
                    } else {
                        return messages;
                    }
                })
                setCurrent(res.data.data.current_page)
                setLast(res.data.data.last_page)
            })
            .catch(error => {
                console.log('error: ' + error)
            })
            .finally(() => {
                setLoading(false)
            })
    }

