import {ActivityIndicator, Platform, Pressable, StyleSheet, Text, TouchableOpacity, View} from "react-native";
import React, {useEffect, useState} from "react";
import {SheetManager} from "react-native-actions-sheet";
import {TimePickerModal} from "react-native-paper-dates";
import DateTimePicker from "@react-native-community/datetimepicker";
import {useApi} from "../hooks/api";
import {useToast} from "react-native-toast-notifications";

export default function CalendarScreen({navigation, route}) {

    const onDismiss = () => {
        setShow(null)
    }


    const onConfirm = (
        ({hours, minutes}) => {

            let t = new Date()
            t.setHours(hours, minutes, 0);

            setSelectedDays(prevState => {
                prevState.hours[show] = t
                return {
                    ...prevState
                }
            })
            setShow(null);
        }
    );

    const {loading, getReminders, storeReminders} = useApi()

    const [errors, setErrors] = useState(null);
    const [status, setStatus] = useState(null);
    const [show, setShow] = useState(null);
    const [date, setDate] = useState(new Date(1598051730000));

    const [selectedDays, setSelectedDays] = useState({
            days: [],
            hours: {
                first: null,
                second: null,
            }
        }
    );

    useEffect(() => {
        getReminders({setStatus, setErrors, setReminders: setSelectedDays})
    }, [])

    useEffect(() => {
        // console.log(selectedDays)
    }, [selectedDays])


    const getSelectedTime = () => {
        return show === 'first' ?
            selectedDays.hours.first ? new Date(selectedDays.hours.first) : new Date()
            :
            selectedDays.hours.second ? new Date(selectedDays.hours.second) : new Date()
    }

    const getHour = () => {
        return getSelectedTime().getHours()
    }

    const getMin = () => {
        return getSelectedTime().getMinutes()
    }

    const onChange = (event, selectedDate) => {
        // note: selectedTime is in GMT format
        setSelectedDays(prevState => {
            prevState.hours[show] = selectedDate
            return {
                ...prevState
            }
        })
        setShow(null);
    };

    const showTimepicker = (type) => {
        setShow(type)
    };

    const styles = StyleSheet.create({
        centeredView: {
            direction: 'rtl',
            justifyContent: 'center',
            alignItems: 'center',
            marginTop: 22,
        },
        modalView: {
            direction: 'rtl',
            margin: 20,
            width: '80%',
            backgroundColor: '#ededed',
            borderWidth: 2,
            borderColor: 'gray',
            borderRadius: 20,
            padding: 35,
            alignItems: 'center',
            shadowColor: '#000',
            shadowOffset: {
                width: 0,
                height: 2,
            },
            shadowOpacity: 0.25,
            shadowRadius: 4,
            elevation: 5,
        },
        button: {
            borderRadius: 5,
            padding: 10,
            marginHorizontal: 2,
            elevation: 2,
        },
        buttonOpen: {
            backgroundColor: 'rgb(255,142,158)',
        },
        buttonClose: {
            backgroundColor: 'rgba(255,182,193,0.7)',
        },
        textStyle: {
            color: 'white',
            textAlign: 'center',
            fontFamily: 'DanaRegular',
        },
        modalText: {
            marginBottom: 15,
            textAlign: 'center',
            fontFamily: 'DanaRegular'
        },
        loginButton: {
            backgroundColor: '#5352ed',
            paddingVertical: 10,
            borderRadius: 8,
            marginTop: 15,
            marginBottom: 10,
        },
        loginButtonText: {
            color: '#fff',
            fontFamily: 'DanaBold',
            alignSelf: 'center',
            fontSize: 18,
        },
        redLoginButton: {
            backgroundColor: '#ff0000',
            paddingVertical: 10,
            borderRadius: 8,
            marginTop: 30,
        },
        redLoginButtonText: {
            color: '#fff',
            fontFamily: 'DanaBold',
            alignSelf: 'center',
            fontSize: 18,
        },
    });

    const toast = useToast();

    useEffect(() => {
        if (status === 'ok') {
            toast.show("   ", {
                type: "success",
                placement: "bottom",
                duration: 3000,
                offset: 50,
                animationType: "slide-in",
            });
        }
    }, [status])


    const days = [
        {
            id: 0,
            name: '',
            full_name: ''
        },
        {
            id: 1,
            name: '',
            full_name: ''
        },
        {
            id: 2,
            name: '',
            full_name: ''
        },
        {
            id: 3,
            name: '',
            full_name: ''
        },
        {
            id: 4,
            name: '',
            full_name: ''
        },
        {
            id: 5,
            name: '',
            full_name: ''
        },
        {
            id: 6,
            name: '',
            full_name: ''
        }
    ]
    const handleRemindersStoring = () => {
        storeReminders({setStatus, setErrors, reminders: selectedDays})
    }
    async function handleDaySelection(day) {

        if (!selectedDays.days.includes(day.id) && selectedDays.days.length >= 3) {
            return alert('        .                 .')
        }

        if (selectedDays.days.includes(day.id)) {
            await SheetManager.show('exercise-day-sheet', {
                payload: {
                    setSelectedDays: setSelectedDays,
                    show_delete_button: true,
                    day: day,
                },
            })
            return
        }

        setSelectedDays((prevState) => {
            prevState.days.push(day.id)
            return {
                ...prevState,
            }
        })

    }

    return (
        <View style={{
            direction: 'rtl',
            backgroundColor: '#f2f2f2',
            width: '100%',
            height: '100%',
            alignItems: 'center'
        }}>
            <View style={{
                marginTop: 15,
                marginBottom: 15,
                alignContent:'center',
                width: '95%',
                backgroundColor: '#ffffff',
                shadowColor: '#000000',
                borderRadius: 5,
                shadowOffset: {
                    width: 0,
                    height: 2,
                },
                shadowOpacity: 0.25,
                shadowRadius: 3.84,
                elevation: 3,
            }}>

                {
                    show !== null && (
                        Platform.OS === 'web' ?
                            <TimePickerModal
                                visible={true}
                                onDismiss={onDismiss}
                                onConfirm={onConfirm}
                                use24HourClock={true}
                                hours={getHour()}
                                minutes={getMin()}
                            />
                            : <DateTimePicker
                                testID="dateTimePicker"
                                value={getSelectedTime()}
                                onChange={onChange}
                                mode='time'
                                is24Hour={true}
                            />

                    )
                }
                <Text style={{
                    paddingRight: 10,
                    paddingTop: 10,
                    fontSize: 16,
                    fontFamily: 'DanaBold',
                }}>  </Text>
                <Text style={{
                    paddingRight: 10,
                    fontSize: 14,
                    color: '#2d2d2d',
                    fontFamily: 'DanaLight',
                }}>
                           
                                 
                </Text>
                <View style={{
                    flexDirection: 'row',
                    margin: 10,
                    width: '100%',
                    justifyContent: 'space-between',
                    marginHorizontal: "auto",
                    paddingHorizontal: 20,
                }}>
                    {
                        loading ? <ActivityIndicator></ActivityIndicator> :
                            days.map((day) => {
                                return (
                                    <Pressable key={day.id} onPress={() => {
                                        handleDaySelection(day)
                                    }}>

                                        <View style={{
                                            justifyContent: 'center',
                                            backgroundColor: selectedDays.days.includes(day.id)
                                                ? '#40c7ff' : '#f2f2f2',
                                            borderRadius: 10,
                                            padding: 15,
                                        }}>
                                            <Text style={{
                                                fontSize: 12,
                                                // width:10,
                                                fontFamily: 'DanaDemiBold',
                                                textAlign: 'center',
                                                color: '#000000'
                                            }}>{day.name}</Text>
                                        </View>
                                    </Pressable>
                                )
                            })
                    }
                </View>
                <View
                    style={{
                        paddingHorizontal: 20,
                        // height: '60%',
                        flexDirection: 'row',
                        width: '100%'
                    }}>
                    <View style={{flexGrow: 1}}>

                        <Text style={{
                            paddingTop: 5,
                            fontSize: 14,
                            fontFamily: 'DanaBold',
                        }}>  </Text>

                        <TouchableOpacity onPress={() => {
                            showTimepicker('first')
                        }} style={{
                            marginTop: 5,
                            backgroundColor: '#f2f2f2',
                            borderRadius: 10,
                        }}>
                            <Text style={{
                                paddingRight: 10,
                                paddingVertical: 5,
                                fontSize: 18,
                                color: '#111111',
                                textAlign: 'center',
                                fontFamily: 'DanaBold',
                            }}>{ loading ? <ActivityIndicator></ActivityIndicator> : selectedDays.hours.first ? new Date(selectedDays.hours.first).getHours() + ':' + new Date(selectedDays.hours.first).getMinutes() : ''}</Text>
                        </TouchableOpacity>

                        <Text style={{
                            paddingTop: 5,
                            fontSize: 14,
                            fontFamily: 'DanaBold',
                        }}>  </Text>

                        <TouchableOpacity onPress={() => {
                            showTimepicker('second')
                        }} style={{
                            marginTop: 5,
                            backgroundColor: '#f2f2f2',
                            borderRadius: 10,
                        }}>
                            <Text style={{
                                paddingRight: 10,
                                paddingVertical: 5,
                                fontSize: 18,
                                color: '#111111',
                                textAlign: 'center',
                                fontFamily: 'DanaBold',
                            }}>{loading ? <ActivityIndicator></ActivityIndicator> : selectedDays.hours.second ? new Date(selectedDays.hours.second).getHours() + ':' + new Date(selectedDays.hours.second).getMinutes() : ''}</Text>
                        </TouchableOpacity>

                        <TouchableOpacity style={styles.loginButton} onPress={() => {
                            handleRemindersStoring()
                        }}>
                            {
                                loading ? <ActivityIndicator></ActivityIndicator> :
                                    <Text style={styles.loginButtonText}>  </Text>

                            }
                        </TouchableOpacity>

                    </View>
                </View>
            </View>
        </View>


    );
}
