import {
    ActivityIndicator,
    Alert,
    Button,
    Modal,
    Pressable,
    StyleSheet,
    Text,
    TouchableOpacity,
    View
} from "react-native";
import React, {useEffect, useState} from "react";
import {SheetManager, SheetProvider} from "react-native-actions-sheet";
import {useApi} from "../hooks/api";

export default function ExamScreen({navigation, route}) {

    const {getExams, loading} = useApi()

    const [exams, setExams] = useState([]);
    const [errors, setErrors] = useState(null);
    const [status, setStatus] = useState(null);

    useEffect(() => {
        getExams({setStatus, setErrors, setExams})
    }, [])

    return (
        <View style={{
            direction: 'rtl',
            backgroundColor: '#f2f2f2',
            width: '100%',
            height: '100%',
            alignItems: 'center',
            alignContent: 'center'
        }}>

            {
                loading ? <View style={{flex: 1, marginTop: 20}}>
                        <ActivityIndicator size={60}></ActivityIndicator>
                    </View>
                    :
                    exams.length === 0 ?

                        <View style={{
                            marginTop: 15,
                            width: '95%',
                            backgroundColor: '#ffffff',
                            shadowColor: '#000000',
                            borderRadius: 5,
                            shadowOffset: {
                                width: 0,
                                height: 2,
                            },
                            shadowOpacity: 0.25,
                            shadowRadius: 3.84,
                            elevation: 3,
                            paddingBottom: 10,
                        }}>
                            <Text style={{
                                paddingRight: 10,
                                paddingTop: 10,
                                fontSize: 16,
                                fontFamily: 'DanaBold',
                            }}>    </Text>
                            <Text style={{
                                paddingRight: 10,
                                fontSize: 14,
                                color: '#2d2d2d',
                                fontFamily: 'DanaLight',
                            }}>          .</Text>
                        </View>
                        :
                        exams.map((exam) => {
                            return <View key={exam.id} style={{
                                marginTop: 15,
                                width: '95%',
                                backgroundColor: '#ffffff',
                                shadowColor: '#000000',
                                borderRadius: 5,
                                shadowOffset: {
                                    width: 0,
                                    height: 2,
                                },
                                shadowOpacity: 0.25,
                                shadowRadius: 3.84,
                                elevation: 3,
                                paddingBottom: 10,
                            }}>
                                <Text style={{
                                    paddingRight: 10,
                                    paddingTop: 10,
                                    fontSize: 16,
                                    fontFamily: 'DanaBold',
                                }}>{exam.title}</Text>
                                <Text style={{
                                    paddingRight: 10,
                                    fontSize: 14,
                                    color: '#2d2d2d',
                                    fontFamily: 'DanaLight',
                                }}>{exam.questions.length}  </Text>

                                <TouchableOpacity onPress={() => {
                                    navigation.navigate('StartExamScreen', {
                                        exam: exam
                                    })
                                }} style={{
                                    width: '90%',
                                    backgroundColor: 'rgba(241,41,67,0.7)',
                                    paddingVertical: 5,
                                    borderRadius: 5,
                                    alignSelf: 'center',
                                    marginTop: 10,
                                }}>
                                    <Text style={{
                                        color: '#fff',
                                        fontFamily: 'DanaBold',
                                        alignSelf: 'center',
                                        fontSize: 14,
                                    }}> </Text>
                                </TouchableOpacity>
                            </View>
                        })
            }


        </View>

    );
}

