import {Alert, Button, Modal, Pressable, SafeAreaView, StyleSheet, Text, TouchableOpacity, View} from "react-native";
import React, {useContext, useEffect, useRef, useState} from "react";
import ReactNativePinView from "react-native-pin-view"
import {Ionicons} from "@expo/vector-icons";
import {useStorage} from "../hooks/data";
import LoadingScreen from "./LoadingScreen";

export default function PasswordSettingsScreen({navigation, route}) {

    const styles = StyleSheet.create({});

    const {getData, storeData, removeData} = useStorage()

    const [hasPincode, setHasPincode] = useState(null);
    const [errors, setErrors] = useState(null);
    const [status, setStatus] = useState(null);
    const [level, setLevel] = useState('first');

    const pinView = useRef(null)

    const [showRemoveButton, setShowRemoveButton] = useState(false)
    const [showCompletedButton, setShowCompletedButton] = useState(false)

    const [enteredPin, setEnteredPin] = useState("")
    const [confirmPin, setConfirmPin] = useState("")

    useEffect(() => {
        getData('pincode').then(value => {
            if (value !== null && value !== undefined && value !== '') {
                setHasPincode(true)
            } else {
                setHasPincode(false)
            }
        })
    }, [])


    useEffect(() => {
        if (enteredPin.length > 0) {
            setShowRemoveButton(true)
        } else {
            setShowRemoveButton(false)
        }
        if (enteredPin.length === 4) {
            setShowCompletedButton(true)
        } else {
            setShowCompletedButton(false)
        }
    }, [enteredPin])

    useEffect(() => {
        if (confirmPin.length > 0) {
            setShowRemoveButton(true)
        } else {
            setShowRemoveButton(false)
        }
        if (confirmPin.length === 4) {
            setShowCompletedButton(true)
        } else {
            setShowCompletedButton(false)
        }
    }, [confirmPin])

    return (
        hasPincode === null ? <LoadingScreen></LoadingScreen> :
            hasPincode === false ?
                <SafeAreaView
                    style={{
                        flex: 1,
                        backgroundColor: "rgb(180,84,102)",
                        justifyContent: "center",
                        alignItems: "center"
                    }}>
                    <Text
                        style={{
                            paddingTop: 24,
                            textAlign: 'center',
                            paddingBottom: 48,
                            color: "rgba(255,255,255,0.7)",
                            fontSize: 20,
                            fontFamily: 'DanaRegular',
                        }}>
                        {level === 'first' && '     '}
                        {level === 'second' && '        '}
                        {level === 'second' && ' (    )'}
                    </Text>
                    <ReactNativePinView
                        inputSize={32}
                        ref={pinView}
                        pinLength={4}
                        buttonSize={60}
                        onValueChange={
                            level === 'first' ?
                                value => setEnteredPin(value)
                                :
                                value => setConfirmPin(value)
                        }
                        buttonAreaStyle={{
                            marginTop: 24,
                        }}
                        inputAreaStyle={{
                            marginBottom: 24,
                        }}
                        inputViewEmptyStyle={{
                            backgroundColor: "transparent",
                            borderWidth: 1,
                            borderColor: "#FFF",
                        }}
                        inputViewFilledStyle={{
                            backgroundColor: "#FFF",
                        }}
                        buttonViewStyle={{
                            borderWidth: 1,
                            borderColor: "#FFF",
                        }}
                        buttonTextStyle={{
                            color: "#FFF",
                        }}
                        onButtonPress={key => {
                            if (key === "custom_left") {
                                pinView.current.clear()
                            }
                            if (key === "custom_right") {
                                if (level === 'first') {
                                    setLevel('second')
                                    pinView.current.clearAll()
                                    setShowCompletedButton(false)
                                } else if ((level === 'second')) {
                                    if (enteredPin === confirmPin) {
                                        storeData('pincode', String(enteredPin)).then(r => {
                                            alert('     .           .')
                                        }).then(() => {
                                            navigation.goBack()
                                        })
                                    } else {
                                        alert('        .       .')
                                    }
                                }
                            }
                        }}
                        customLeftButton={showRemoveButton ?
                            <Ionicons name="backspace" size={36} color="#FFF"/> : undefined}
                        customRightButton={showCompletedButton ?
                            <Ionicons name="md-shield-checkmark" size={36} color="#FFF"/> : undefined}
                    />
                </SafeAreaView>
                :
                <View style={{width: '100%', alignItems: 'center', justifyContent: 'center'}}>
                    <View style={{
                        width: '95%',
                        backgroundColor: '#ffffff',
                        shadowColor: '#000000',
                        borderRadius: 5,
                        shadowOffset: {
                            width: 0,
                            height: 2,
                        },
                        shadowOpacity: 0.25,
                        shadowRadius: 3.84,
                        elevation: 3,
                        marginTop: 10,
                    }}>
                        <Text style={{
                            paddingRight: 10,
                            paddingVertical: 10,
                            fontSize: 14,
                            fontFamily: 'DanaBold',
                        }}>  </Text>
                        <Text style={{
                            paddingRight: 10,
                            paddingVertical: 10,
                            fontSize: 14,
                            fontFamily: 'DanaRegular',
                        }}>      .          .</Text>

                        <TouchableOpacity onPress={() => {
                            removeData('pincode').then(()=>{
                                navigation.goBack()
                            }).then(()=>{
                                alert('     .          .')
                            })
                        }} style={{
                            width: '90%',
                            backgroundColor: 'rgba(241,41,67,0.7)',
                            paddingVertical: 5,
                            borderRadius: 5,
                            alignSelf: 'center',
                            marginTop: 10,
                            marginBottom: 10,
                        }}>
                            <Text style={{
                                color: '#fff',
                                fontFamily: 'DanaBold',
                                alignSelf: 'center',
                                fontSize: 14,
                            }}>  </Text>
                        </TouchableOpacity>
                    </View>

                </View>

    );
}

