import {Alert, Button, Modal, Pressable, SafeAreaView, StyleSheet, Text, TouchableOpacity, View} from "react-native";
import React, {useContext, useEffect, useRef, useState} from "react";
import ReactNativePinView from "react-native-pin-view"
import {Ionicons} from "@expo/vector-icons";
import {useStorage} from "../hooks/data";
import {MainContext} from "../Contexts/MainContext";
export default function PincodeScreen({navigation, route}) {

    const {setShowPinCodeScreen} = useContext(MainContext)

    const styles = StyleSheet.create({});

    const {getData} = useStorage()

    const [errors, setErrors] = useState(null);
    const [status, setStatus] = useState(null);
    const pinView = useRef(null)
    const [showRemoveButton, setShowRemoveButton] = useState(false)
    const [showCompletedButton, setShowCompletedButton] = useState(false)
    const [enteredPin, setEnteredPin] = useState("")

    useEffect(() => {
        if (enteredPin.length > 0) {
            setShowRemoveButton(true)
        } else {
            setShowRemoveButton(false)
        }
        if (enteredPin.length === 4) {
            setShowCompletedButton(true)
        } else {
            setShowCompletedButton(false)
        }
    }, [enteredPin])

    const checkPincode = async () => {
       getData('pincode').then((value)=>{
           if(value === String(enteredPin)){
               setShowPinCodeScreen(false)
           }else{
               alert('   .    .')
           }
       })
    };

    return (
        <SafeAreaView
            style={{flex: 1, backgroundColor: "rgb(180,84,102)", justifyContent: "center", alignItems: "center"}}>
            <Text
                style={{
                    paddingTop: 24,
                    textAlign: 'center',
                    paddingBottom: 48,
                    color: "rgba(255,255,255,0.7)",
                    fontSize: 20,
                    fontFamily: 'DanaRegular',
                }}>    </Text>
            <ReactNativePinView
                inputSize={32}
                ref={pinView}
                pinLength={4}
                buttonSize={60}
                onValueChange={
                    value => setEnteredPin(value)
                }
                buttonAreaStyle={{
                    marginTop: 24,
                }}
                inputAreaStyle={{
                    marginBottom: 24,
                }}
                inputViewEmptyStyle={{
                    backgroundColor: "transparent",
                    borderWidth: 1,
                    borderColor: "#FFF",
                }}
                inputViewFilledStyle={{
                    backgroundColor: "#FFF",
                }}
                buttonViewStyle={{
                    borderWidth: 1,
                    borderColor: "#FFF",
                }}
                buttonTextStyle={{
                    color: "#FFF",
                }}
                onButtonPress={async key => {
                    if (key === "custom_left") {
                        pinView.current.clear()
                    }
                    if (key === "custom_right") {
                        await checkPincode()
                    }
                }}
                customLeftButton={showRemoveButton ? <Ionicons name="backspace" size={36} color="#FFF"/> : undefined}
                customRightButton={showCompletedButton ?
                    <Ionicons name="md-shield-checkmark" size={36} color="#FFF"/> : undefined}
            />
        </SafeAreaView>
    );
}

