import {ScrollView, StyleSheet, Text, TouchableOpacity, View} from "react-native";
import React, {useEffect, useState} from "react";
import Fab from "../../components/Fab";
import {useApi} from "../../hooks/api";
import LoadingScreen from "../LoadingScreen";
import {useIsFocused} from "@react-navigation/native";
import {SheetManager} from "react-native-actions-sheet";

export default function NotesScreen({stackNavigation, route}) {

    const styles = StyleSheet.create({
        paragraph: {},
    })

    const {loading, getNotes} = useApi()
    const [errors, setErrors] = useState(null);
    const [status, setStatus] = useState(null);
    const [notes, setNotes] = useState([])

    const getNotesAsync = async () => {
        await getNotes({setStatus, setErrors}).then((r) => {
            if (r !== undefined)
                setNotes(r)
        })
    }

    const isFocused = useIsFocused();

    useEffect(() => {
        isFocused && getNotesAsync()
    }, [isFocused]);

    useEffect(() => {
        getNotesAsync()
    }, [])

    return (
        <View style={{flex: 1, justifyContent: 'center', alignItems: 'center'}}>
            <ScrollView
                style={{
                    width: '100%'
                }}>
                <View style={{
                    direction: 'rtl',
                    backgroundColor: '#f2f2f2',
                    width: '100%',
                    height: '100%',
                    alignItems: 'center',
                    marginBottom: 10
                }}>
                    {loading ? <LoadingScreen/> :
                        <View
                            style={{
                                width: '95%'
                            }}>
                            {
                                notes.map((note, index) => {
                                    return (
                                        <TouchableOpacity
                                            key={note.id}
                                            onPress={() => {
                                                stackNavigation.navigate('EditNoteScreen', {
                                                    type: 'edit',
                                                    note: note
                                                })
                                            }}
                                            onLongPress={() => {
                                                console.log('long')
                                                SheetManager.show('delete-note-sheet', {
                                                    payload: {
                                                        note: note,
                                                        getNotesAsync
                                                    },
                                                })
                                            }}
                                        >
                                            <View
                                                style={{
                                                    marginTop: 15,
                                                    backgroundColor: '#ffcc29',
                                                    shadowColor: '#000000',
                                                    borderRadius: 5,
                                                    shadowOffset: {
                                                        width: 0,
                                                        height: 2,
                                                    },
                                                    shadowOpacity: 0.25,
                                                    shadowRadius: 3.84,
                                                    elevation: 3,
                                                }}>
                                                <View style={{}}>
                                                    <Text
                                                        numberOfLines={3}
                                                        ellipsizeMode="tail"
                                                        style={{
                                                            paddingHorizontal: 10,
                                                            paddingTop: 10,
                                                            paddingBottom: 20,
                                                            fontSize: 14,
                                                            fontFamily: 'DanaRegular',
                                                        }}>{note.text}</Text>
                                                </View>
                                            </View>

                                        </TouchableOpacity>
                                    )
                                })
                            }
                        </View>
                    }
                </View>
            </ScrollView>
            <Fab
                title="+"
                onPress={
                    () => {
                        stackNavigation.navigate('EditNoteScreen', {
                            type: 'new'
                        })
                    }}
            ></Fab>
        </View>


    );
}
