import {
    ActivityIndicator,
    Alert,
    Button,
    Modal,
    Pressable,
    StyleSheet,
    Text,
    TouchableOpacity,
    View
} from "react-native";
import React, {useEffect, useState} from "react";
import {SheetManager, SheetProvider} from "react-native-actions-sheet";
import Video from '../components/Video'
import {useApi} from "../hooks/api";
import {useToast} from "react-native-toast-notifications";

export default function SingleExerciseScreen({navigation, route}) {

    const [errors, setErrors] = useState(null);
    const [status, setStatus] = useState(null);

    const {loading, storeExercisePractice} = useApi();

    const handleExercisePractice = function (){
        storeExercisePractice({setStatus, setErrors, slug: route?.params.item.slug})
    }

    const toast = useToast();

    useEffect(() => {
        if (status === 'ok') {
            toast.show("!       .", {
                type: "success",
                placement: "bottom",
                duration: 3000,
                offset: 50,
                animationType: "slide-in",

            });
            setStatus(null)
        }
    }, [status])

    const styles = StyleSheet.create({
        loginButton: {
            backgroundColor: 'rgba(233,30,99,0.76)',
            paddingVertical: 10,
            borderRadius: 8,
            marginTop: 15,
            marginHorizontal: 5,
            marginBottom: 10,
        },
        loginButtonText: {
            color: '#fff',
            fontFamily: 'DanaBold',
            alignSelf: 'center',
            fontSize: 18,
        },
        centeredView: {
            direction: 'rtl',
            justifyContent: 'center',
            alignItems: 'center',
            marginTop: 22,
        },
        modalView: {
            direction: 'rtl',
            margin: 20,
            width: '80%',
            backgroundColor: '#ededed',
            borderWidth: 2,
            borderColor: 'gray',
            borderRadius: 20,
            padding: 35,
            alignItems: 'center',
            shadowColor: '#000',
            shadowOffset: {
                width: 0,
                height: 2,
            },
            shadowOpacity: 0.25,
            shadowRadius: 4,
            elevation: 5,
        },
        button: {
            borderRadius: 5,
            padding: 10,
            marginHorizontal: 2,
            elevation: 2,
        },
        buttonOpen: {
            backgroundColor: 'rgb(255,142,158)',
        },
        buttonClose: {
            backgroundColor: 'rgba(255,182,193,0.7)',
        },
        textStyle: {
            color: 'white',
            textAlign: 'center',
            fontFamily: 'DanaRegular',
        },
        modalText: {
            marginBottom: 15,
            textAlign: 'center',
            fontFamily: 'DanaRegular'
        },
    });

    React.useEffect(() => {
        navigation.setOptions({
            title: route?.params.item.title === '' ? '' : route?.params.item.title,
        });
    }, [navigation, route?.params.item.title]);


    return (

        <View style={{
            direction: 'rtl',
            backgroundColor: '#f2f2f2',
            width: '100%',
            height: '100%',
            alignItems: 'center'
        }}>

            <View style={{
                marginTop: 15,
                width: '95%',
                backgroundColor: '#ffffff',
                shadowColor: '#000000',
                borderRadius: 5,
                shadowOffset: {
                    width: 0,
                    height: 2,
                },
                shadowOpacity: 0.25,
                shadowRadius: 3.84,
                elevation: 3,
            }}>
                <Text style={{
                    paddingRight: 10,
                    paddingTop: 10,
                    fontSize: 16,
                    fontFamily: 'DanaBold',
                }}>{route.params?.item.title}</Text>
                <Text style={{
                    padding: 10,
                    fontSize: 14,
                    color: '#2d2d2d',
                    marginBottom: 10,
                    fontFamily: 'DanaLight',
                }}>                   
                    !</Text>

                <Video url={route.params?.item.url}></Video>

                <TouchableOpacity onPress={() => {
                    handleExercisePractice()
                }} style={styles.loginButton}>
                    {
                        loading ? <ActivityIndicator></ActivityIndicator> :
                            <Text style={styles.loginButtonText}>   !</Text>
                    }
                </TouchableOpacity>
            </View>


        </View>

    );
}


