import React, {createContext, useCallback, useEffect, useState} from 'react';
import {I18nManager, StyleSheet, Platform, Text, ActivityIndicator, View, StatusBar, Button} from 'react-native';
import {useFonts} from "expo-font";
import AsyncStorage from "@react-native-async-storage/async-storage";
import {NavigationContainer} from "@react-navigation/native";
import {createNativeStackNavigator} from "@react-navigation/native-stack";
import {reloadAsync} from 'expo-updates';
import HomeScreen from "./Screens/HomeScreen";
import ArmFormScreen from "./Screens/ArmFormScreen";
import LymphedemaScreen from "./Screens/LymphedemaScreen";
import ExercisesScreen from "./Screens/ExercisesScreen";
import MoodScreen from "./Screens/MoodScreen";
import IntroSliderScreen from "./Screens/IntroSliderScreen";
import {useAuth} from "./hooks/auth";
import LoginScreen from "./Screens/LoginScreen";
import {SheetProvider} from "react-native-actions-sheet";
import './sheets/sheets';
import SingleExerciseScreen from "./Screens/SingleExerciseScreen";
import ExamScreen from "./Screens/ExamScreen";
import GroupChatScreen from "./Screens/GroupChatScreen";
import NotebookScreen from "./Screens/NotebookScreen";
import LoadingScreen from "./Screens/LoadingScreen";
import {ToastProvider} from "react-native-toast-notifications";
import {MainContext} from "./Contexts/MainContext";
import {useStorage} from "./hooks/data";
import SupportScreen from "./Screens/SupportScreen";
import FeedbackScreen from "./Screens/FeedbackScreen";
import SettingsScreen from "./Screens/SettingsScreen";
import StartExamScreen from "./Screens/StartExamScreen";
import {useApi} from "./hooks/api";
import PasswordSettingsScreen from "./Screens/PasswordSettingsScreen";
import PincodeScreen from "./Screens/PincodeScreen";
import EditNoteScreen from "./Screens/NotebookScreens/EditNoteScreen";
import * as SplashScreen from 'expo-splash-screen';
import {Ionicons} from "@expo/vector-icons";
import {AntDesign} from '@expo/vector-icons';
import HelpScreen from "./Screens/HelpScreen";
import {helpScreenData} from "./data/HelpScreenData";
import CalendarScreen from "./Screens/CalendarScreen";

// Keep the splash screen visible while we fetch resources
SplashScreen.preventAutoHideAsync();

export default function App() {
    // make application RTL
    if (Platform.OS !== 'web' && !I18nManager.isRTL) {
        I18nManager.allowRTL(true);
        I18nManager.forceRTL(true);
        reloadAsync()
    }
    I18nManager.forceRTL(true);

    const {getData} = useStorage()
    const {getFeeling} = useApi()
    const auth = useAuth()
    const Stack = createNativeStackNavigator();
    const [showIntro, setShowIntro] = useState(null); // change to null
    const [showMoodPage, setShowMoodPage] = useState(false);
    const [showPinCodeScreen, setShowPinCodeScreen] = useState(null);
    const [mood, setMood] = useState(null)
    const [status, setStatus] = useState();
    const [errors, setErrors] = useState();
    const [appIsReady, setAppIsReady] = useState(false);
    getData('show_intro_slider').then(value => setShowIntro(!(value === 'false')))

    const getHeader = (navigation, route) => {
        return <AntDesign
            onPress={() => {
                navigation.navigate('HelpScreen', {
                    ...helpScreenData.filter((item) =>
                        route.name === item.screen
                    )[0]
                })
            }}
            name="questioncircleo"
            size={24}
            color="black"/>
    }

    // load fonts
    const [loaded] = useFonts({
        DanaBlack: require('./assets/fonts/Dana-Black.ttf'),
        DanaBold: require('./assets/fonts/Dana-Bold.ttf'),
        DanaDemiBold: require('./assets/fonts/Dana-DemiBold.ttf'),
        DanaExtraBlack: require('./assets/fonts/Dana-ExtraBlack.ttf'),
        DanaExtraBold: require('./assets/fonts/Dana-ExtraBold.ttf'),
        DanaFat: require('./assets/fonts/Dana-Fat.ttf'),
        DanaHairline: require('./assets/fonts/Dana-Hairline.ttf'),
        DanaHeavy: require('./assets/fonts/Dana-Heavy.ttf'),
        DanaLight: require('./assets/fonts/Dana-Light.ttf'),
        DanaMedium: require('./assets/fonts/Dana-Medium.ttf'),
        DanaRegular: require('./assets/fonts/Dana-Regular.ttf'),
        DanaThin: require('./assets/fonts/Dana-Thin.ttf'),
        DanaUltraLight: require('./assets/fonts/Dana-UltraLight.ttf'),
    });

    async function prepare() {
        setAppIsReady(true);
        await SplashScreen.hideAsync();
        // try {
        //     // Pre-load fonts, make any API calls you need to do here
        // } catch (e) {
        //     console.warn(e);
        // } finally {
        //     // Tell the application to render
        //     setAppIsReady(true);
        //     await SplashScreen.hideAsync();
        // }
    }

    useEffect(() => {
        getData('pincode').then(value => {
            if (value !== null && value !== undefined && value !== '') {
                setShowPinCodeScreen(true)
            } else {
                setShowPinCodeScreen(false)
            }
        })

        prepare()
    }, [])

    // ask for mood if already is not answered
    useEffect(() => {
        if (auth.isLoggedIn) {
            getFeeling({setStatus, setErrors}).then((feeling) => {
                if (!feeling?.feeling) {
                    setShowMoodPage(true)
                }
            })
        }
    }, [auth.isLoggedIn])

    if (!appIsReady || !loaded) {
        return null;
    }

    return (
        <MainContext.Provider
            value={{
                setShowIntro: setShowIntro,
                setShowMoodPage: setShowMoodPage,
                auth: auth,
                setShowPinCodeScreen: setShowPinCodeScreen
            }}
        >
            <StatusBar backgroundColor={'rgba(255,93,116,0.7)'}/>
            {
                appIsReady &&
                showPinCodeScreen === null ? <LoadingScreen></LoadingScreen> :
                    showPinCodeScreen === true ? <PincodeScreen/> :
                        showIntro ? <IntroSliderScreen></IntroSliderScreen> :
                            <ToastProvider textStyle={{fontFamily: 'DanaRegular'}}>
                                {
                                    auth.loading && <LoadingScreen></LoadingScreen>
                                }

                                {
                                    auth.isLoggedIn === null ? <LoadingScreen></LoadingScreen> :
                                        auth.isLoggedIn && showMoodPage ? <MoodScreen></MoodScreen>
                                            :
                                            <SheetProvider>
                                                <NavigationContainer>
                                                    <Stack.Navigator>
                                                        {auth.isLoggedIn ? (
                                                            <>
                                                                <Stack.Screen name="Home"
                                                                              component={HomeScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      title: '  ',
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                  }
                                                                              )}/>
                                                                <Stack.Screen name="LymphedemaScreen"
                                                                              component={LymphedemaScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      title: '  ',
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                      headerRight: () => (
                                                                                          getHeader(navigation, route)
                                                                                      )
                                                                                  }
                                                                              )}/>
                                                                <Stack.Screen name="ExercisesScreen"
                                                                              component={ExercisesScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      title: '    ',
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                      headerRight: () => (
                                                                                          getHeader(navigation, route)
                                                                                      )
                                                                                  }
                                                                              )}/>
                                                                <Stack.Screen name="ArmFormScreen"
                                                                              component={ArmFormScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      title: '    ',
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                      headerRight: () => (
                                                                                          getHeader(navigation, route)
                                                                                      )
                                                                                  }
                                                                              )}/>
                                                                <Stack.Screen name="ExamScreen"
                                                                              component={ExamScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      title: ' ',
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                      headerRight: () => (
                                                                                          getHeader(navigation, route)
                                                                                      )
                                                                                  }
                                                                              )}/>
                                                                <Stack.Screen name="GroupChatScreen"
                                                                              component={GroupChatScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      title: ' ',
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                      headerRight: () => (
                                                                                          getHeader(navigation, route)
                                                                                      )
                                                                                  }
                                                                              )}/>
                                                                <Stack.Screen name="NotebookScreen"
                                                                              component={NotebookScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      title: '  ',
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                      headerRight: () => (
                                                                                          getHeader(navigation, route)
                                                                                      )
                                                                                  }
                                                                              )}/>
                                                                <Stack.Screen name="SingleExerciseScreen"
                                                                              component={SingleExerciseScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      title: route.params.title,
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                  }
                                                                              )}/>
                                                                <Stack.Screen name="SupportScreen"
                                                                              component={SupportScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      title: '  ',
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                      headerRight: () => (
                                                                                          getHeader(navigation, route)
                                                                                      )
                                                                                  }
                                                                              )}/>
                                                                <Stack.Screen name="FeedbackScreen"
                                                                              component={FeedbackScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      title: '   ',
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                      headerRight: () => (
                                                                                          getHeader(navigation, route)
                                                                                      )
                                                                                  }
                                                                              )}/>
                                                                <Stack.Screen name="SettingsScreen"
                                                                              component={SettingsScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      title: '',
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                  }
                                                                              )}/>
                                                                <Stack.Screen name="StartExamScreen"
                                                                              component={StartExamScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      title: '',
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                  }
                                                                              )}/>
                                                                <Stack.Screen name="PasswordSettingsScreen"
                                                                              component={PasswordSettingsScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      title: '  ',
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                  }
                                                                              )}/>
                                                                <Stack.Screen name="EditNoteScreen"
                                                                              component={EditNoteScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                  }
                                                                              )}/>
                                                                <Stack.Screen name="MoodScreen"
                                                                              component={MoodScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      title: '   ',
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                  }
                                                                              )}/>
                                                                <Stack.Screen name="HelpScreen"
                                                                              component={HelpScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      title: '',
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                  }
                                                                              )}/>
                                                                <Stack.Screen name="CalendarScreen"
                                                                              component={CalendarScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      title: '',
                                                                                      headerTitleAlign: 'center',
                                                                                      headerStyle: {
                                                                                          backgroundColor: '#FFB6C1',
                                                                                      },
                                                                                      headerTintColor: '#000000',
                                                                                      headerTitleStyle: {
                                                                                          fontFamily: 'DanaBold'
                                                                                      },
                                                                                      headerRight: () => (
                                                                                          getHeader(navigation, route)
                                                                                      )
                                                                                  }
                                                                              )}/>
                                                            </>
                                                        ) : (
                                                            <>
                                                                <Stack.Screen name="Login"
                                                                              component={LoginScreen}
                                                                              options={({route, navigation}) => (
                                                                                  {
                                                                                      headerShown: false
                                                                                  }
                                                                              )}/>
                                                            </>
                                                        )}
                                                    </Stack.Navigator>
                                                </NavigationContainer>
                                            </SheetProvider>
                                }
                            </ToastProvider>
            }
        </MainContext.Provider>
    );
}
