import {View} from "react-native";
import React from "react";

const GridList = (props) => {

    return (
        <View style={styles.app}>
            {
                props.items.map((item) => {
                    return (
                        <props.template
                            key={item.id}
                            item={item}
                            {...props}>
                        </props.template>
                    )
                })
            }
        </View>
    );
};

export default GridList;

const styles = {
    app: {
        direction: 'rtl',
        marginTop: 10,
        justifyContent: 'center',
        marginHorizontal: "auto",
        flexDirection: "row",
        flexWrap: "wrap"
    },
};
