import {ScrollView, StyleSheet, Text, TextInput, TouchableOpacity, View} from "react-native";
import React, {useEffect, useState} from "react";
import Fab from "../../components/Fab";
import {useApi} from "../../hooks/api";
import LoadingScreen from "../LoadingScreen";
import {useIsFocused} from "@react-navigation/native";
import {SheetManager} from "react-native-actions-sheet";
import {WeeklyQuestions} from "../../data/WeeklyQuestions";
import {FeedbackQuestions} from "../../data/FeedbackQuestions";
import Video from "../../components/Video";

export default function FeedbackScreen({stackNavigation, route}) {

    const styles = StyleSheet.create({
        paragraph: {},
    })

    const {loading, getWeeklyReports, storeWeeklyReport} = useApi()
    const [errors, setErrors] = useState(null);
    const [status, setStatus] = useState(null);
    const [feedbacks, setFeedbacks] = useState([])
    const [goal, setGoal] = useState('')
    const [events, setEvents] = useState('')

    const getWeeklyReportsAsync = async () => {
        await getWeeklyReports({setStatus, setErrors}).then((r) => {
            if (r) {
                setFeedbacks(r.data?.feedbacks ? r.data?.feedbacks : [])
                setGoal(r.data?.goal ? r.data?.goal : "")
                setEvents(r.data?.events ? r.data?.events : "")
            }
        })
    }

    useEffect(() => {
        getWeeklyReportsAsync()
    }, [])

    const selectOption = (prevState, item, option) => {
        const old = prevState.filter(question => !(item.id === question.question_id));
        return [
            ...old,
            {
                question_id: item.id,
                answer_id: option.id,
                question: item,
                answer: option
            }
        ];
    };

    const handleStoreFeedback = () => {
        if (feedbacks.length !== WeeklyQuestions.length) {
            alert('     .')
        } else {
            storeWeeklyReport({
                setErrors,
                setStatus,
                type: 'feedback',
                feedbacks: feedbacks,
                goal: goal
            }).then((r) => console.log(r))
        }
    };

    const handleStoreGoal = () => {
        storeWeeklyReport({
            setErrors,
            setStatus,
            type: 'simonton',
            events: events
        })
    };

    return (
        <ScrollView>

            {
                loading ? <LoadingScreen></LoadingScreen>
                    :
                    <>


                        <Text style={{
                            paddingRight: 20,
                            paddingTop: 10,
                            fontSize: 18,
                            fontFamily: 'DanaBold',
                        }}>  </Text>

                        <View
                            style={{
                                direction: 'rtl',
                                backgroundColor: '#f2f2f2',
                                width: '100%',
                                height: '100%',
                                alignItems: 'center',
                                marginTop: 5
                            }}>
                            {
                                WeeklyQuestions.map((item) => {
                                    return (
                                        <View
                                            key={item.id}
                                            style={{
                                                width: '95%'
                                            }}>
                                            <View style={{
                                                marginBottom: 15,
                                                backgroundColor: '#ffffff',
                                                shadowColor: '#000000',
                                                borderRadius: 5,
                                                shadowOffset: {
                                                    width: 0,
                                                    height: 2,
                                                },
                                                shadowOpacity: 0.25,
                                                shadowRadius: 3.84,
                                                elevation: 3,
                                            }}>
                                                <View style={{
                                                    direction: 'rtl',
                                                    flexDirection: 'row'
                                                }}>
                                                    <Text style={{
                                                        paddingRight: 10,
                                                        paddingTop: 10,
                                                        fontSize: 18,
                                                        fontFamily: 'DanaRegular',
                                                    }}>{item.question}</Text>
                                                </View>
                                                {
                                                    item.options.map((option) => {
                                                        return (
                                                            <View
                                                                key={option.id}
                                                                style={{
                                                                    paddingHorizontal: 10,
                                                                    marginBottom: 10,
                                                                }}
                                                            >
                                                                <TouchableOpacity
                                                                    onPress={() => {
                                                                        setFeedbacks((prevState) => selectOption(prevState, item, option))
                                                                    }}
                                                                    style={{
                                                                        backgroundColor: feedbacks?.filter((answer) => {
                                                                            return answer.question_id === item.id && answer.answer_id === option.id
                                                                        }).length === 1 ? 'rgba(255,182,193,0.7)' : 'transparent',
                                                                        paddingVertical: 5,
                                                                        borderRadius: 10,
                                                                        borderColor: '#464646',
                                                                        borderWidth: 1,
                                                                        marginTop: 10,
                                                                    }}>
                                                                    <Text style={{
                                                                        color: '#1c1c1c',
                                                                        fontFamily: 'DanaBold',
                                                                        alignSelf: 'center',
                                                                        fontSize: 14,
                                                                    }}>{option.value}</Text>
                                                                </TouchableOpacity>
                                                            </View>
                                                        )
                                                    })
                                                }
                                            </View>
                                        </View>
                                    )
                                })

                            }

                            <View
                                style={{
                                    width: '95%'
                                }}>
                                <View style={{
                                    marginBottom: 5,
                                    backgroundColor: '#ffffff',
                                    shadowColor: '#000000',
                                    borderRadius: 5,
                                    shadowOffset: {
                                        width: 0,
                                        height: 2,
                                    },
                                    shadowOpacity: 0.25,
                                    shadowRadius: 3.84,
                                    elevation: 3,
                                }}>
                                    <View>
                                        <Text style={{
                                            padding: 10,
                                            fontSize: 18,
                                            fontFamily: 'DanaRegular',
                                        }}>               
                                             
                                            :
                                        </Text>

                                        <View style={{
                                            padding: 5
                                        }}>
                                            <TextInput
                                                style={{
                                                    textAlignVertical: "top",
                                                    textAlign: 'right',
                                                    padding: 10,
                                                    borderColor: 'red',
                                                    borderWidth: 2,
                                                    borderRadius: 5,
                                                }}
                                                multiline={true}
                                                numberOfLines={4}
                                                onChangeText={(text) => setGoal(text)}
                                                value={goal}/>
                                        </View>


                                    </View>

                                </View>
                            </View>


                            <TouchableOpacity style={{
                                width: '95%',
                                backgroundColor: 'rgba(241,41,67,0.7)',
                                paddingVertical: 10,
                                borderRadius: 8,
                                marginBottom: 10,
                            }} onPress={() => {
                                handleStoreFeedback()
                            }}>
                                {
                                    <Text style={{
                                        color: '#fff',
                                        fontFamily: 'DanaBold',
                                        alignSelf: 'center',
                                        fontSize: 18,
                                    }}> </Text>
                                }
                            </TouchableOpacity>


                            <View
                                style={{
                                    width: '95%'
                                }}>
                                <View style={{
                                    marginBottom: 15,
                                    backgroundColor: '#ffffff',
                                    shadowColor: '#000000',
                                    borderRadius: 5,
                                    shadowOffset: {
                                        width: 0,
                                        height: 2,
                                    },
                                    shadowOpacity: 0.25,
                                    shadowRadius: 3.84,
                                    elevation: 3,
                                }}>

                                    <Text style={{
                                        paddingRight: 20,
                                        paddingTop: 10,
                                        fontSize: 18,
                                        fontFamily: 'DanaBold',
                                    }}> </Text>

                                    <Text style={{
                                        marginBottom: 5,
                                        paddingRight: 20,
                                        paddingTop: 10,
                                        fontSize: 18,
                                        fontFamily: 'DanaRegular',
                                    }}>                
                                        .              
                                                       
                                            .         
                                                    - 
                                           .           
                                                .         
                                                        
                                                    .  
                                                      .</Text>

                                    <Video
                                        url='https://lymphedema-app.s3.ir-thr-at1.arvanstorage.ir/-.m4v'></Video>

                                    <Text style={{
                                        paddingRight: 20,
                                        paddingTop: 10,
                                        fontSize: 18,
                                        fontFamily: 'DanaRegular',
                                    }}>                
                                           :</Text>

                                    <View style={{
                                        padding: 5
                                    }}>
                                        <TextInput
                                            style={{
                                                textAlignVertical: "top",
                                                textAlign: 'right',
                                                padding: 10,
                                                borderColor: 'red',
                                                borderWidth: 2,
                                                borderRadius: 5,
                                            }}
                                            multiline={true}
                                            numberOfLines={4}
                                            onChangeText={(text) => setEvents(text)}
                                            value={events}/>
                                    </View>

                                    <View style={{
                                        paddingHorizontal: 5,
                                    }}>
                                        <TouchableOpacity style={{
                                            backgroundColor: 'rgba(241,41,67,0.7)',
                                            paddingVertical: 10,
                                            borderRadius: 8,
                                            marginBottom: 10,
                                        }} onPress={() => {
                                            handleStoreGoal()
                                        }}>
                                            {
                                                <Text style={{
                                                    color: '#fff',
                                                    fontFamily: 'DanaBold',
                                                    alignSelf: 'center',
                                                    fontSize: 18,
                                                }}></Text>
                                            }
                                        </TouchableOpacity>
                                    </View>

                                </View>
                            </View>

                        </View>


                    </>
            }


        </ScrollView>
    );
}
