import {
    ActivityIndicator,
    Alert,
    Button,
    Modal,
    Pressable,
    StyleSheet,
    Text,
    TouchableOpacity,
    View
} from "react-native";
import React, {useEffect, useState} from "react";
import {SheetManager, SheetProvider} from "react-native-actions-sheet";
import {useApi} from "../hooks/api";
import {FeedbackQuestions} from "../data/FeedbackQuestions";
import {Ionicons} from "@expo/vector-icons";

export default function StartExamScreen({navigation, route}) {

    const [current, setCurrent] = useState(0);
    const [answers, setAnswers] = useState([]);
    const [answerStatus, setAnswerStatus] = useState('question');
    const [showAnswer, setShowAnswer] = useState()

    const [errors, setErrors] = useState(null);
    const [status, setStatus] = useState(null);

    const {loading, storeExam} = useApi()

    // this function checks if selected answer is correct or not
    const isAnswerCorrect = async () => {
        const currentQuestion = route?.params.exam.questions[current]
        const selectedAnswer = answers.filter((answer) => {
            return answer.question_id === currentQuestion.id
        })
        return selectedAnswer[0].option.is_correct_answer
    }

    const handleNext = async () => {

        if (answerStatus === 'question') {
            setAnswerStatus('answer')
        } else {
            let length = route?.params.exam.questions.length
            if (current + 1 < length) {
                setCurrent(prevState => prevState + 1)
                setAnswerStatus('question')
            } else {
                storeExam({setStatus,setErrors,exam: route?.params.exam, answers: answers }).then(() => {
                    setAnswerStatus('finish')
                })
            }
        }

    }

    // useEffect(() => {
    //     console.log(route?.params.exam.questions)
    // }, [route?.params.exam])

    const handleAnswerSelection = (question_id, option) => {
        setAnswers((prevState) => {
            prevState = prevState.filter((item) => {
                return item.question_id !== question_id
            })

            return [
                ...prevState, {
                    question_id: question_id,
                    option: option
                }
            ]
        })
    }

    React.useEffect(() => {
        navigation.setOptions({
            title: route?.params.exam.title === '' ? '' : route?.params.exam.title,
        });

    }, [navigation, route?.params.exam]);

    return (
        <View style={{
            flex: 1, alignItems: 'center', justifyContent: 'center',
            direction: 'rtl',
            backgroundColor: '#f2f2f2',
            width: '100%',
            height: '100%',
            alignContent: 'center'
        }}>

            <View
                style={{
                    width: '90%',
                    backgroundColor: '#ffffff',
                    shadowColor: '#000000',
                    borderRadius: 5,
                    shadowOffset: {
                        width: 0,
                        height: 2,
                    },
                    shadowOpacity: 0.25,
                    shadowRadius: 3.84,
                    elevation: 3,
                }}>

                {
                    answerStatus === 'finish' ?
                        <View style={{
                            alignContent: 'center',
                            alignItems: 'center',
                            paddingVertical: 30,
                        }}>
                            {
                                loading ? <ActivityIndicator size={80}></ActivityIndicator>:
                                <>

                                    <Ionicons name="checkmark-circle" size={100} color="green"/>
                                    <Text style={{
                                        paddingRight: 10,
                                        paddingTop: 10,
                                        fontSize: 16,
                                        fontFamily: 'DanaBold',
                                    }}> ! </Text>


                                    <View style={{
                                        direction: 'rtl',
                                        flexDirection: 'row'
                                    }}>
                                        <Text style={{
                                            paddingRight: 10,
                                            paddingTop: 10,
                                            fontSize: 16,
                                            fontFamily: 'DanaRegular',
                                        }}>         !</Text>
                                    </View>
                                </>
                            }


                        </View> :
                        <View>
                            <Text style={{
                                paddingRight: 10,
                                paddingTop: 10,
                                fontSize: 16,
                                fontFamily: 'DanaBold',
                            }}> {current + 1} </Text>


                            <View style={{
                                direction: 'rtl',
                                flexDirection: 'row'
                            }}>
                                <Text style={{
                                    paddingRight: 10,
                                    paddingTop: 10,
                                    fontSize: 16,
                                    fontFamily: 'DanaBold',
                                }}>{route?.params.exam.questions[current].title}</Text>
                            </View>
                            {
                                answerStatus === 'answer' &&
                                route?.params.exam.questions[current].descriptive_answer &&
                                <View>
                                    <View
                                        style={{
                                            alignSelf: 'center',
                                            width: '95%',
                                            paddingRight: 10,
                                            paddingTop: 10,
                                            borderWidth: 2,
                                            borderRadius: 10,
                                            borderColor: '#f2f2f2',
                                            fontSize: 16,
                                            fontFamily: 'DanaBold',
                                            marginTop: 10,
                                            backgroundColor: '#ffcc29'
                                        }}>

                                        <Text style={{
                                            paddingRight: 10,
                                            marginHorizontal: 20,
                                            fontSize: 16,
                                            fontFamily: 'DanaBold',
                                        }}>!
                                        </Text>

                                        <Text style={{
                                            paddingRight: 10,
                                            marginHorizontal: 20,
                                            fontSize: 16,
                                            fontFamily: 'DanaRegular',
                                            marginBottom: 10,
                                        }}>{route?.params.exam.questions[current].descriptive_answer} </Text>
                                    </View>
                                </View>
                            }
                            {
                                answerStatus === 'answer' ?
                                    <View
                                        style={{
                                            paddingHorizontal: 10,
                                            marginBottom: 10,
                                        }}
                                    >
                                        {
                                            route?.params.exam.questions[current].options.map((option, index) => {
                                                return <View
                                                    key={index}
                                                    style={{
                                                        backgroundColor:

                                                            answerStatus === 'answer' && option.is_correct_answer === true
                                                                ?
                                                                'rgb(62,157,62)'
                                                                :
                                                                answers.filter((answer) => {
                                                                    return answer.question_id === route?.params.exam.questions[current].id && answer.option === option
                                                                }).length === 1 ? 'rgba(255,182,193,0.7)' : 'transparent',
                                                        paddingVertical: 5,
                                                        borderRadius: 10,
                                                        borderColor: '#464646',
                                                        borderWidth: 1,
                                                        marginTop: 10,
                                                    }}>
                                                    <Text style={{
                                                        color: '#1c1c1c',
                                                        fontFamily: 'DanaBold',
                                                        alignSelf: 'center',
                                                        fontSize: 14,
                                                    }}>{option.option}</Text>
                                                </View>
                                            })
                                        }
                                        {
                                            answerStatus === 'question' ?
                                                <TouchableOpacity onPress={() => {
                                                    handleNext()
                                                }} style={{
                                                    width: '100%',
                                                    backgroundColor: 'rgba(241,41,67,0.7)',
                                                    paddingVertical: 5,
                                                    borderRadius: 5,
                                                    alignSelf: 'center',
                                                    marginTop: 20,
                                                }}>
                                                    <Text style={{
                                                        color: '#fff',
                                                        fontFamily: 'DanaBold',
                                                        alignSelf: 'center',
                                                        fontSize: 14,
                                                    }}> </Text>
                                                </TouchableOpacity>
                                                :
                                                <TouchableOpacity onPress={() => {
                                                    handleNext()
                                                }} style={{
                                                    width: '100%',
                                                    backgroundColor: 'rgba(241,41,67,0.7)',
                                                    paddingVertical: 5,
                                                    borderRadius: 5,
                                                    alignSelf: 'center',
                                                    marginTop: 20,
                                                }}>
                                                    <Text style={{
                                                        color: '#fff',
                                                        fontFamily: 'DanaBold',
                                                        alignSelf: 'center',
                                                        fontSize: 14,
                                                    }}> </Text>
                                                </TouchableOpacity>

                                        }
                                    </View>
                                    :
                                    <View
                                        style={{
                                            paddingHorizontal: 10,
                                            marginBottom: 10,
                                        }}
                                    >
                                        {
                                            route?.params.exam.questions[current].options.map((option, index) => {
                                                return <TouchableOpacity
                                                    key={index}
                                                    onPress={() => {
                                                        handleAnswerSelection(route?.params.exam.questions[current].id, option)
                                                    }}
                                                    style={{
                                                        backgroundColor: answers.filter((answer) => {
                                                            return answer.question_id === route?.params.exam.questions[current].id && answer.option === option
                                                        }).length === 1 ? answerStatus === 'answer' && option.is_correct_answer === true ? 'rgb(62,157,62)' : 'rgba(255,182,193,0.7)' : 'transparent',
                                                        paddingVertical: 5,
                                                        borderRadius: 10,
                                                        borderColor: '#464646',
                                                        borderWidth: 1,
                                                        marginTop: 10,
                                                    }}>
                                                    <Text style={{
                                                        color: '#1c1c1c',
                                                        fontFamily: 'DanaBold',
                                                        alignSelf: 'center',
                                                        fontSize: 14,
                                                    }}>{option.option}</Text>
                                                </TouchableOpacity>
                                            })
                                        }
                                        {
                                            answerStatus === 'question' ?
                                                <TouchableOpacity onPress={() => {
                                                    handleNext()
                                                }} style={{
                                                    width: '100%',
                                                    backgroundColor: 'rgba(241,41,67,0.7)',
                                                    paddingVertical: 5,
                                                    borderRadius: 5,
                                                    alignSelf: 'center',
                                                    marginTop: 20,
                                                }}>
                                                    <Text style={{
                                                        color: '#fff',
                                                        fontFamily: 'DanaBold',
                                                        alignSelf: 'center',
                                                        fontSize: 14,
                                                    }}> </Text>
                                                </TouchableOpacity>
                                                :
                                                <TouchableOpacity onPress={() => {
                                                    handleNext()
                                                }} style={{
                                                    width: '100%',
                                                    backgroundColor: 'rgba(241,41,67,0.7)',
                                                    paddingVertical: 5,
                                                    borderRadius: 5,
                                                    alignSelf: 'center',
                                                    marginTop: 20,
                                                }}>
                                                    <Text style={{
                                                        color: '#fff',
                                                        fontFamily: 'DanaBold',
                                                        alignSelf: 'center',
                                                        fontSize: 14,
                                                    }}> </Text>
                                                </TouchableOpacity>

                                        }
                                    </View>

                            }

                        </View>

                }

            </View>

        </View>

    );
}

