import {Dimensions, StyleSheet, Text, TouchableOpacity, View} from "react-native";
import MyModal from "../../components/MyModal";
import LoadingScreen from "../LoadingScreen";
import {BarChart} from "react-native-chart-kit";
import {convertRtl} from "react-native-rtl-reshaper";
import {useApi} from "../../hooks/api";
import {useEffect, useState} from "react";

const styles = StyleSheet.create({
    centeredView: {
        direction: 'rtl',
        justifyContent: 'center',
        alignItems: 'center',
        marginTop: 22,
    },
    modalView: {
        direction: 'rtl',
        margin: 20,
        width: '80%',
        backgroundColor: '#ededed',
        borderWidth: 2,
        borderColor: 'gray',
        borderRadius: 20,
        padding: 35,
        alignItems: 'center',
        shadowColor: '#000',
        shadowOffset: {
            width: 0,
            height: 2,
        },
        shadowOpacity: 0.25,
        shadowRadius: 4,
        elevation: 5,
    },
    button: {
        borderRadius: 5,
        padding: 10,
        marginHorizontal: 2,
        elevation: 2,
    },
    buttonOpen: {
        backgroundColor: 'rgb(255,142,158)',
    },
    buttonClose: {
        backgroundColor: 'rgba(255,182,193,0.7)',
    },
    textStyle: {
        color: 'white',
        textAlign: 'center',
        fontFamily: 'DanaRegular',
    },
    modalText: {
        marginBottom: 15,
        textAlign: 'center',
        fontFamily: 'DanaRegular'
    },
});

const ExerciseCharts = () => {

    const filterOptions = [
        '',
        ' ',
        '',
        ' ',
        ' ',
        ' ',
        '   ',
    ];

    const [errors, setErrors] = useState(null);
    const [status, setStatus] = useState(null);
    const [data, setData] = useState(null);
    const [duration, setDuration] = useState('');
    const {loading, getReports} = useApi()

    useEffect(() => {
        const getData = async () => {
            await getReports({setStatus, setErrors, duration: duration, type: 'exercise'}).then((r) => {
                setData(r)
            })
        }
        getData()
    }, [duration])

    const [modalVisible, setModalVisible] = useState(false);

    return (
        <View style={{
            marginTop: 15,
            width: '95%',
            backgroundColor: '#ffffff',
            shadowColor: '#000000',
            borderRadius: 5,
            shadowOffset: {
                width: 0,
                height: 2,
            },
            shadowOpacity: 0.25,
            shadowRadius: 3.84,
            elevation: 3,
        }}>
            <View style={{
                flexDirection: 'row'
            }}>
                <View style={{
                    padding: 10,
                    flexDirection: 'row-reverse'
                }}>
                    <TouchableOpacity
                        onPress={() => {
                            setModalVisible(true)
                        }}
                        style={{
                            paddingVertical: 5,
                            paddingHorizontal: 10,
                            borderRadius: 10,
                            borderColor: '#464646',
                            borderWidth: 1,
                            marginTop: 10,
                        }}>
                        <Text style={{
                            color: '#2d2d2d',
                            fontFamily: 'DanaLight',
                            alignSelf: 'center',
                            fontSize: 14,
                        }}>{duration}</Text>
                    </TouchableOpacity>
                </View>
            </View>
            <Text style={{
                paddingRight: 10,
                marginTop: 10,
                fontSize: 16,
                fontFamily: 'DanaBold',
            }}>  </Text>
            <View style={{
                flexDirection: 'row-reverse',
            }}>
                <MyModal
                    view={
                        <View>
                            <Text style={styles.modalText}>  </Text>

                            {
                                filterOptions.map((item, index) => {
                                    return <TouchableOpacity
                                        onPress={() => {
                                            setDuration(item)
                                            setModalVisible(!modalVisible)
                                        }}
                                        key={index}
                                        style={{
                                            paddingVertical: 5,
                                            borderRadius: 10,
                                            borderColor: '#464646',
                                            borderWidth: 1,
                                            marginTop: 10,
                                        }}>
                                        <Text style={{
                                            color: '#1c1c1c',
                                            fontFamily: 'DanaBold',
                                            alignSelf: 'center',
                                            fontSize: 14,
                                        }}>{item}</Text>
                                    </TouchableOpacity>
                                })
                            }

                        </View>
                    }
                    modalVisible={modalVisible} setModalVisible={setModalVisible}></MyModal>
            </View>
            <View>
                <View style={{
                    alignSelf: 'stretch',
                    flexDirection: 'row',
                    paddingHorizontal: 10,
                }}>
                    {
                        data !== null && data !== undefined && Object.keys(data).map((item, index) => {
                            return (
                                <View
                                    key={index}
                                    style={{
                                        flex: 1,
                                        flexDirection: 'column',
                                        alignSelf: 'stretch'
                                    }}>
                                    <Text style={{
                                        fontSize: 12,
                                        textAlign: 'center',
                                        fontFamily: 'DanaBold',
                                        marginBottom: 10,
                                    }}>{item}</Text>
                                    {
                                        <>
                                            <View
                                                style={{
                                                    flex: 1,
                                                    alignItems: 'center',
                                                    alignSelf: 'stretch'
                                                }}>
                                                <Text style={{
                                                    fontSize: 12,
                                                    textAlign: 'center',
                                                    fontFamily: 'DanaRegular',
                                                    marginBottom: 10,
                                                }}>{data[item]}  </Text>
                                            </View>
                                        </>
                                    }
                                </View>
                            )
                        })
                    }
                </View>
            </View>
            <Text style={{
                paddingRight: 10,
                paddingTop: 10,
                fontSize: 16,
                marginTop: 10,
                fontFamily: 'DanaBold',
            }}>  </Text>
            <View style={{
                alignContent: 'center',
                alignItems: 'center',
            }}>
                {
                    loading || data === null ? <LoadingScreen></LoadingScreen> :
                        data !== undefined && Object.keys(data).length > 0 ?
                            <BarChart
                                style={{
                                    marginVertical: 8,
                                    borderRadius: 5,
                                    fontFamily: 'DanaRegular',
                                }}
                                data={{
                                    labels: Object.keys(data).map((item) => {
                                        return convertRtl(item)
                                    }),
                                    datasets: [
                                        {
                                            data: [...Object.values(data)]
                                        }
                                    ]
                                }}
                                width={Dimensions.get("window").width - 30}
                                height={220}
                                showValuesOnTopOfBars={true}
                                chartConfig={{
                                    backgroundColor: "rgba(255,107,128,0.93)",
                                    backgroundGradientFrom: "rgba(255,143,156,0.7)",
                                    backgroundGradientTo: "rgb(255,89,116)",
                                    decimalPlaces: 0,
                                    color: (opacity = 1) => `rgba(255, 255, 255, ${opacity})`,
                                    labelColor: (opacity = 1) => `rgba(255, 255, 255, ${opacity})`,
                                }}
                                verticalLabelRotation={0}
                            /> :
                            <View style={{marginTop: 40, marginBottom: 20}}>
                                <Text style={{
                                    fontFamily: 'DanaRegular'
                                }}>      .</Text>
                            </View>
                }
            </View>
        </View>
    )
}

export default ExerciseCharts
