import React, {useContext, useEffect} from 'react';
import {
    View,
    Text,
    Image,
    StyleSheet,
    StatusBar,
    I18nManager, Platform, Button, Pressable,
} from 'react-native';
import AppIntroSlider from 'react-native-app-intro-slider';
import Icon from '@expo/vector-icons/Ionicons';
import AsyncStorage from "@react-native-async-storage/async-storage";
import {MainContext} from "../Contexts/MainContext";
import {useStorage} from "../hooks/data";

const data = [
    {
        id:1,
        text: '   \n       \n   !',
        image: require('../assets/images/breast-cancer-ribbon.png'),
        bg: '#FFB6C1',
        has_start_button: true
    }
];

const IntroSliderScreen = () => {

    const {setShowIntro} = useContext(MainContext)

    const {storeData} = useStorage()

    const _renderItem = ({item}) => {

        return (
            <View
                key={item.id}
                style={[
                    styles.slide,
                    {
                        backgroundColor: item.bg,
                    },
                ]}>
                <Text style={styles.title}>{item.title}</Text>
                <Image source={item.image} style={styles.image}/>
                <Text style={styles.text}>{item.text}</Text>
                {
                    item.has_start_button &&
                    <Pressable style={styles.button} onPress={() => {
                        setShowIntro(false)
                        storeData('show_intro_slider', 'false')
                    }}>
                        <Text style={styles.startButton}>!</Text>
                    </Pressable>
                }
            </View>
        );
    };
    const _keyExtractor = (item) => item.id;
    const _renderNextButton = () => {
        return (
            <View style={styles.buttonCircle}>
                <Icon name="md-arrow-forward-outline"
                      color="rgba(255, 255, 255, .9)"
                      size={24}
                />
            </View>
        );
    };
    const _renderPrevButton = () => {
        return (
            <View style={styles.buttonCircle}>
                <Icon name="md-arrow-back-outline"
                      color="rgba(255, 255, 255, .9)"
                      size={24}
                />
            </View>
        );
    };
    const _renderDoneButton = () => {
        return (
            <View style={styles.buttonCircle}>
                <Icon
                    name="md-checkmark"
                    color="rgba(255, 255, 255, .9)"
                    size={24}
                    onPress={() => {
                        setShowIntro(false)
                        storeData('show_mood_page', 'false')
                    }}
                />
            </View>
        );
    };

    return (
        <View style={{flex: 1}}>
            <StatusBar translucent backgroundColor="transparent"/>
            <AppIntroSlider
                keyExtractor={_keyExtractor}
                renderItem={_renderItem}
                renderDoneButton={_renderDoneButton}
                renderNextButton={_renderNextButton}
                renderPrevButton={_renderPrevButton}
                showPrevButton
                // nextLabel={''}
                // prevLabel={''}
                // doneLabel={'!'}
                data={data}
            />
        </View>)
}

export default IntroSliderScreen

const styles = StyleSheet.create({
    buttonCircle: {
        width: 40,
        height: 40,
        backgroundColor: 'rgba(0, 0, 0, .2)',
        borderRadius: 20,
        justifyContent: 'center',
        alignItems: 'center',
    },
    slide: {
        height: Platform.OS === 'web' ? '100vh' : '100%',
        // flex: 1,
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: 'blue',
    },
    image: {
        width: 220,
        height: 220,
        marginVertical: 32,
        resizeMode: 'contain'
    },
    text: {
        fontFamily: 'DanaRegular',
        // color: 'rgba(255, 255, 255, 0.8)',
        color: '#000000',
        textAlign: 'center',
    },
    title: {
        fontFamily: 'DanaBold',
        fontSize: 30,
        color: '#000000',
        textAlign: 'center',
    },
    button: {
        marginTop: 20,
        alignItems: 'center',
        justifyContent: 'center',
        paddingVertical: 12,
        paddingHorizontal: 32,
        borderRadius: 4,
        elevation: 3,
        backgroundColor: '#2196F3',
    },
    startButton: {
        fontSize: 16,
        lineHeight: 21,
        fontWeight: 'bold',
        letterSpacing: 0.25,
        color: 'white',
        fontFamily: 'DanaRegular',

    }
});
