import {ActivityIndicator, View} from "react-native";
import {Button, Image, Pressable, Text} from "react-native";
import React, {useContext, useEffect, useState} from "react";
import {MainContext} from "../Contexts/MainContext";
import {useStorage} from "../hooks/data";
import {useApi} from "../hooks/api";

const MoodScreen = (props) => {

        const {setShowMoodPage} = useContext(MainContext)
        const [selectedMood, setSelectedMood] = useState(null)
        const [errors, setErrors] = useState(null);
        const [status, setStatus] = useState(null);
        const {storeData} = useStorage()
        const {loading, getFeeling, storeFeeling} = useApi()
        const items = [
            {
                id: 1,
                image: '',
                text: ' ',
            },
            {
                id: 2,
                image: '',
                text: '',
            },
            {
                id: 3,
                image: '',
                text: ' ',
            },
            {
                id: 4,
                image: '',
                text: '',
            },
            {
                id: 5,
                image: '',
                text: ' ',
            },
        ];

        useEffect(() => {
            getFeeling({setStatus, setErrors}).then((feeling) => {
                if (feeling?.feeling) {
                    setSelectedMood(parseInt(feeling?.feeling))
                }
            })

        }, [])


        const handleSubmitButton = async () => {
            await storeFeeling({setStatus, setErrors, feeling: selectedMood}).then(() => {
                setShowMoodPage(false)
                if (props?.navigation) {
                    props.navigation.goBack()
                }
            })
        };

        return (

            <View style={{
                backgroundColor: '#FFB6C1', flexDirection: 'column', flex: 1, justifyContent: 'center',
                marginHorizontal: "auto",
            }}>
                {
                    loading ?
                        <ActivityIndicator size={40}></ActivityIndicator> :
                        <>

                            <Text style={{fontSize: 20, marginBottom: 40, fontFamily: 'DanaBold', textAlign: 'center'}}>!
                                 
                                </Text>
                            <View style={styles.app}>
                                <View style={{
                                    flexDirection: 'row',
                                    flex: 1,
                                    margin: 10,
                                    justifyContent: 'space-between',
                                    marginHorizontal: "auto",
                                    paddingHorizontal: 30,
                                }}>

                                    {
                                        items.map((item) => {
                                            return (
                                                <Pressable key={item.id} onPress={() => {
                                                    setSelectedMood(item.id)
                                                }}>
                                                    <View style={{justifyContent: 'center'}}>

                                                        <Text style={{
                                                            borderRadius: 100,
                                                            padding: selectedMood === item.id ? 10 : 0,
                                                            backgroundColor: selectedMood === item.id ? '#f2f2f2' : 'transparent',
                                                            fontSize: 30,
                                                            textAlign: 'center',
                                                            color: '#fff'
                                                        }}>{item.image}</Text>

                                                        <Text style={{
                                                            fontSize: 12,
                                                            marginTop: 5,
                                                            fontFamily: 'DanaBold',
                                                            textAlign: 'center',
                                                            color: '#363636'
                                                        }}>{item.text}</Text>
                                                    </View>
                                                </Pressable>

                                            )
                                        })
                                    }

                                </View>
                            </View>
                            {
                                selectedMood && <View style={{paddingHorizontal: 100, marginTop: 20,}}>
                                    <Pressable style={styles.button} onPress={() => {

                                        handleSubmitButton()
                                        // storeData('show_mood_page', 'false')

                                    }}>
                                        <Text style={{
                                            color: '#363636',
                                            textAlign: 'center',
                                            fontSize: 16,
                                            fontFamily: 'DanaBold',
                                        }}>
                                            
                                            </Text>
                                    </Pressable>
                                </View>
                            }
                        </>
                }
            </View>
        );
    }
;

export default MoodScreen;


const styles = {
    app: {
        marginTop: 10,
        padding: 5,
        justifyContent: 'center',
        marginHorizontal: "auto",
        width: 400,
        flexDirection: "row",
        flexWrap: "wrap"
    },
    button: {
        alignItems: 'center',
        justifyContent: 'center',
        paddingVertical: 12,
        paddingHorizontal: 32,
        borderRadius: 4,
        elevation: 3,
        backgroundColor: '#f2f2f2',
    },
};
