import React, {useRef, useState} from 'react';
import {
    Button, Platform, StyleSheet,
    Text, TouchableOpacity,
    View,
} from 'react-native';
import ActionSheet, {
    ActionSheetRef, SheetManager,
    SheetProps,
} from 'react-native-actions-sheet';

function ExerciseDaySelection({sheetId, payload}: SheetProps<{ data: string }>) {

    const actionSheetRef = useRef<ActionSheetRef>(null);

    const styles = StyleSheet.create({
        loginButton: {
            backgroundColor: '#5352ed',
            paddingVertical: 10,
            borderRadius: 8,
            marginTop: 30,
        },
        loginButtonText: {
            color: '#fff',
            fontFamily: 'DanaBold',
            alignSelf: 'center',
            fontSize: 18,
        },
        redLoginButton: {
            backgroundColor: '#ff0000',
            paddingVertical: 10,
            borderRadius: 8,
            marginTop: 30,
        },
        redLoginButtonText: {
            color: '#fff',
            fontFamily: 'DanaBold',
            alignSelf: 'center',
            fontSize: 18,
        },
    })

    return (
        <ActionSheet
            id={sheetId}
            ref={actionSheetRef}
            containerStyle={{
                borderTopLeftRadius: 25,
                borderTopRightRadius: 25
            }}
            indicatorStyle={{
                width: 100
            }}
            gestureEnabled={true}>

            <View
                style={{
                    padding: 20,
                    // height: '60%',
                    flexDirection: 'row',
                    width: '100%'
                }}>

                <View style={{flexGrow: 1}}>
                    {
                        payload.show_delete_button &&
                        <TouchableOpacity style={styles.redLoginButton} onPress={() => {
                            payload.setSelectedDays((prevState) => {
                                prevState.days = prevState.days.filter(item => item !== payload.day.id)
                                return {
                                    ...prevState
                                }
                            })
                            SheetManager.hide("exercise-day-sheet");
                        }}>
                            <Text style={styles.redLoginButtonText}> 
                                 {payload.day.full_name}</Text>
                        </TouchableOpacity>
                    }
                </View>
            </View>
        </ActionSheet>
    );
}


export default ExerciseDaySelection;
