import React, {useContext, useEffect, useState} from 'react';
import {
    StyleSheet,
    Text,
    View,
    Image,
    TextInput,
    TouchableOpacity,
    TouchableWithoutFeedback,
    Dimensions,
    Keyboard,
    ActivityIndicator
} from 'react-native';
import {Icon} from 'react-native-elements';
import * as Animatable from 'react-native-animatable';
import BackgroundImage from '../assets/images/bg-3-min.jpg';
import {useAuth} from "../hooks/auth";
import {MainContext} from "../Contexts/MainContext";

export default function LoginScreen({navigation, route}) {

    const {auth} = useContext(MainContext)

    const [status, setStatus] = useState(null)
    const [loading, setLoading] = useState(false)
    const [phone, setPhone] = useState("")
    const [askForName, setAskForName] = useState(false)
    const [askForAge, setAskForAge] = useState(false)
    const [firstName, setFirstName] = useState("")
    const [lastName, setLastName] = useState("")
    const [age, setAge] = useState("")
    const [code, setCode] = useState("")
    const [errors, setErrors] = useState()

    const sendSmsCode = function () {
        if (status === null) {
            auth.login({setErrors, setStatus, phone}).then((res) => {
                if(res){
                    if (res.ask_for_name === true) {
                        setAskForName(true)
                    }
                    if (res.ask_for_age === true) {
                        setAskForAge(true)
                    }
                }
            })
        } else if (status === "SMS_SENT") {
            auth.login({
                setErrors,
                setStatus,
                phone,
                code,
                is_verification: true,
                first_name: firstName,
                last_name: lastName,
                age: age
            })
        }

    }

    return (
        <View style={styles.container}>
            <View style={{flex: 1}}>
                <Image
                    style={{flex: 1, width: null, marginTop: -500}}
                    source={BackgroundImage}
                />
            </View>
            <Animatable.Text
                style={styles.titleText}
                animation='fadeInUp'
                delay={600}
            >  
            </Animatable.Text>
            <View style={styles.bottomView}>
                <Text style={styles.loginText}></Text>
                {
                    loading || status === "LOADING" ? <ActivityIndicator size={'large'}></ActivityIndicator>
                        :
                        status === "SMS_SENT" ?
                            <>
                                <Text style={styles.registerText}>      </Text>
                                <View style={styles.inputView}>
                                    <TextInput
                                        style={styles.input}
                                        placeholder=' '
                                        keyboardType='number-pad'
                                        textContentType='oneTimeCode'
                                        onChangeText={value => setCode(value)}
                                    />
                                </View>
                                <Text style={styles.registerText}>                 .</Text>
                                {
                                    askForName &&
                                    <>
                                        <View style={styles.inputView}>
                                            <TextInput
                                                style={styles.input}
                                                placeholder=''
                                                keyboardType='default'
                                                textContentType='name'
                                                onChangeText={value => setFirstName(value)}
                                                value={firstName}
                                            />
                                        </View>
                                        <View style={styles.inputView}>
                                            <TextInput
                                                style={styles.input}
                                                placeholder=' '
                                                keyboardType='default'
                                                textContentType='familyName'
                                                onChangeText={value => setLastName(value)}
                                                value={lastName}
                                            />
                                        </View>
                                    </>
                                }
                                {
                                    askForAge !== null && askForAge &&
                                    <>
                                        <View style={styles.inputView}>
                                            <TextInput
                                                style={styles.input}
                                                placeholder=''
                                                keyboardType='number-pad'
                                                textContentType='none'
                                                onChangeText={value => setAge(value)}
                                                value={age}
                                            />
                                        </View>
                                    </>
                                }
                                <TouchableOpacity onPress={() => sendSmsCode()} style={styles.loginButton}>
                                    <Text style={styles.loginButtonText}>   </Text>
                                </TouchableOpacity>
                                {/*<Text style={{marginTop: 20, color: '#5352ed', fontFamily: 'DanaBold'}}>*/}
                                {/*       */}
                                {/*</Text>*/}
                            </>
                            :
                            <>
                                <View style={styles.inputView}>
                                    <Icon
                                        style={styles.inputIcon}
                                        name='phone'
                                        type='ionicons'
                                        color='#5352ed'
                                    />
                                    <TextInput
                                        style={styles.input}
                                        placeholder=' '
                                        autoCapitalize='none'
                                        keyboardType='number-pad'
                                        textContentType='telephoneNumber'
                                        onChangeText={value => setPhone(value)}
                                    />
                                </View>
                                {/*<Text style={styles.fpText}>   </Text>*/}
                                <TouchableOpacity onPress={() => sendSmsCode()} style={styles.loginButton}>
                                    <Text style={styles.loginButtonText}>  </Text>
                                </TouchableOpacity>
                                <Text style={styles.registerText}>
                                                      
                                    
                                    
                                </Text></>

                }

            </View>
        </View>
    );
}

