import {Pressable, Text, View} from "react-native";
import React from "react";

const HomeMenuOption = ({item, navigation}) => {

    const styles = {
        item: {
            direction: 'rtl',
            flex: 1,
            minWidth: 100,
            maxWidth: 100,
            textAlign: 'center',
            height: 120,
            justifyContent: "center",
            alignItems: "center",
            borderRadius: 8,
            padding: 5,
            backgroundColor: "rgba(249, 180, 45, 0.25)",
            borderWidth: 1.5,
            borderColor: "#fff",
            marginHorizontal: 10,
            marginBottom: 10,
        },
        pressBox: {
            flex: 1,
            justifyContent: "center",
            alignItems: "center",
        }
    };

    return (
        <View style={styles.item}>
            <Pressable style={styles.pressBox} onPress={() => {
                navigation.navigate(item.page)
            }}>
                {
                    item.icon
                }
                <Text style={
                    {
                        fontFamily: 'DanaBold',
                        fontSize: 12,
                        marginTop: 10,
                        textAlign: 'center'
                    }
                }>{item.title}</Text>
            </Pressable>
        </View>
    );
};

export default HomeMenuOption
