import React, {useRef, useState} from 'react';
import {
    Button, Platform, StyleSheet,
    Text, TouchableOpacity,
    View,
} from 'react-native';
import ActionSheet, {
    ActionSheetRef, SheetManager,
    SheetProps,
} from 'react-native-actions-sheet';
import {useApi} from "../hooks/api";

function DeleteNote({sheetId, payload}: SheetProps<{ data: string }>) {

    const actionSheetRef = useRef<ActionSheetRef>(null);
    const styles = StyleSheet.create({
        loginButton: {
            backgroundColor: '#5352ed',
            paddingVertical: 10,
            borderRadius: 8,
            marginTop: 30,
        },
        loginButtonText: {
            color: '#fff',
            fontFamily: 'DanaBold',
            alignSelf: 'center',
            fontSize: 18,
        },
        redLoginButton: {
            backgroundColor: '#ff0000',
            paddingVertical: 10,
            borderRadius: 8,
            marginTop: 30,
        },
        redLoginButtonText: {
            color: '#fff',
            fontFamily: 'DanaBold',
            alignSelf: 'center',
            fontSize: 18,
        },
    })
    const {loading, deleteNote} = useApi()
    const [errors, setErrors] = useState(null);
    const [status, setStatus] = useState(null);
    const deleteNoteAsync = async () => {
        await deleteNote({setStatus, setErrors, note_id: payload.note.id})
    };

    return (
        <ActionSheet
            id={sheetId}
            ref={actionSheetRef}
            containerStyle={{
                borderTopLeftRadius: 25,
                borderTopRightRadius: 25
            }}
            indicatorStyle={{
                width: 100
            }}
            gestureEnabled={true}>

            <View
                style={{
                    padding: 20,
                    // height: '60%',
                    flexDirection: 'row',
                    width: '100%'
                }}>
                <View style={{flexGrow: 1}}>
                    {
                        <TouchableOpacity style={styles.redLoginButton} onPress={() => {
                            deleteNoteAsync().then(() => {
                                SheetManager.hide("delete-note-sheet")
                                payload.getNotesAsync()
                            })
                        }}>
                            <Text style={styles.redLoginButtonText}> </Text>
                        </TouchableOpacity>
                    }
                </View>
            </View>
        </ActionSheet>
    );
}


export default DeleteNote;
