import {ScrollView, Text, TextInput, TouchableOpacity, View} from "react-native";
import React, {useEffect, useState} from "react";
import {useApi} from "../hooks/api"
import LoadingScreen from "./LoadingScreen";
import {ArmMeasurementData} from "../data/ArmMeasurement";
import {useToast} from "react-native-toast-notifications";
import Video from "../components/Video";

function ArmFormScreen() {

    const {getArmMeasurement, storeArmMeasurement, loading, error} = useApi()

    const [measurements, setMeasurements] = useState(null);
    const [errors, setErrors] = useState(null);
    const [status, setStatus] = useState(null);

    const toast = useToast();

    useEffect(() => {
        getArmMeasurement({setMeasurements, setErrors})
    }, [])

    // useEffect(() => {
    //     console.log(measurements)
    // }, [measurements])

    useEffect(() => {
        if (status === 'ok') {
            toast.show("   ", {
                type: "success",
                placement: "bottom",
                duration: 3000,
                offset: 50,
                animationType: "slide-in",
            });
        }
    }, [status])

    const handleMeasuringStore = (slug, values) => {
        storeArmMeasurement({setStatus, setErrors, slug, measurements})
    };


    return (
        <ScrollView style={{
            direction: 'rtl'
        }}>
            {
                loading || measurements === null ?
                    <LoadingScreen></LoadingScreen> :
                    <View style={{width: '100%', alignItems: 'center', justifyContent: 'center'}}>
                        <View style={{
                            marginTop: 15,
                            marginBottom: 15,
                            width: '95%',
                            backgroundColor: '#ffffff',
                            shadowColor: '#000000',
                            borderRadius: 5,
                            shadowOffset: {
                                width: 0,
                                height: 2,
                            },
                            shadowOpacity: 0.25,
                            shadowRadius: 3.84,
                            elevation: 3,
                        }}>
                            <Text style={{
                                paddingRight: 10,
                                paddingVertical: 10,
                                fontSize: 16,
                                fontFamily: 'DanaRegular',
                            }}>                 
                                  
                                 </Text>


                            <Video url='https://lymphedema-app.s3.ir-thr-at1.arvanstorage.ir/-----.m4v'></Video>


                        </View>
                        {
                            ArmMeasurementData.map(item => {
                                return <View key={item.id} style={{width: '95%'}}>

                                    <View style={{
                                        width: '100%',
                                        paddingTop: 10,
                                        backgroundColor: 'rgba(255,182,193,0.7)',
                                        borderRadius: 5,
                                        marginBottom: 8,

                                    }}>
                                        <View style={{paddingHorizontal: 10, marginBottom: 10,}}>

                                            <Text
                                                style={{
                                                    color: 'black',
                                                    fontSize: 18,
                                                    fontFamily: 'DanaBold'
                                                }}>{item.title}</Text>

                                            <View style={{
                                                flexDirection: 'row',
                                                justifyContent: 'space-between',
                                                marginTop: 5,
                                            }}>

                                                <Text
                                                    style={{color: 'black', fontSize: 14, fontFamily: 'DanaBold'}}>
                                                    :</Text>

                                                <TextInput
                                                    style={
                                                        {
                                                            width: '50%',
                                                            borderColor: '#595959',
                                                            borderStyle: 'solid',
                                                            borderRadius: 5,
                                                            borderWidth: 2,
                                                            backgroundColor: 'white',
                                                            paddingHorizontal: 10,
                                                            fontFamily: 'DanaRegular',
                                                            fontSize: 16,
                                                            color: '#333',
                                                        }
                                                    }
                                                    value={measurements[item.slug].right}
                                                    placeholder=''
                                                    autoCapitalize='none'
                                                    keyboardType='number-pad'
                                                    textContentType='telephoneNumber'
                                                    onChangeText={(value) => {
                                                        setMeasurements(prevStat => {
                                                            prevStat[item.slug].right = value
                                                            return {
                                                                ...prevStat
                                                            }
                                                        })
                                                    }}
                                                />
                                            </View>


                                            <View style={{
                                                flexDirection: 'row',
                                                justifyContent: 'space-between',
                                                marginTop: 5,
                                            }}>

                                                <Text
                                                    style={{color: 'black', fontSize: 14, fontFamily: 'DanaBold'}}>
                                                    :</Text>

                                                <TextInput
                                                    style={
                                                        {
                                                            width: '50%',
                                                            borderColor: '#595959',
                                                            borderStyle: 'solid',
                                                            borderRadius: 5,
                                                            borderWidth: 2,
                                                            backgroundColor: 'white',
                                                            paddingHorizontal: 10,
                                                            fontFamily: 'DanaRegular',
                                                            fontSize: 16,
                                                            color: '#333',
                                                        }
                                                    }
                                                    value={measurements[item.slug].left}
                                                    placeholder=''
                                                    autoCapitalize='none'
                                                    keyboardType='number-pad'
                                                    textContentType='telephoneNumber'
                                                    onChangeText={(value) => {
                                                        setMeasurements(prevStat => {
                                                            prevStat[item.slug].left = value
                                                            return {
                                                                ...prevStat
                                                            }
                                                        })
                                                    }}
                                                />
                                            </View>


                                            <TouchableOpacity onPress={() => {
                                                handleMeasuringStore(item.slug)
                                            }} style={styles.loginButton}>
                                                <Text style={styles.loginButtonText}>  </Text>
                                            </TouchableOpacity>

                                        </View>
                                    </View>
                                </View>
                            })
                        }
                    </View>
            }

        </ScrollView>
    );
}

export default ArmFormScreen

const styles = {
    loginButton: {
        backgroundColor: 'rgba(241,41,67,0.7)',
        paddingVertical: 5,
        borderRadius: 5,
        marginTop: 10,
    },
    loginButtonText: {
        color: '#fff',
        fontFamily: 'DanaBold',
        alignSelf: 'center',
        fontSize: 14,
    },
}
